
<template>
  <div style="width:100%;height:100%;">
      <div style="background: #F5F5F5">
        <div class="TopName W95"><i class="el-icon-close" style="margin-right:10px;cursor: pointer;" @click="Back"></i>返回</div>
      </div>
      <div class="TopLine"></div>
        <br/>
        <br/>
      <el-form ref="form" label-width="100px" id="form" method="post" >
        <el-form-item label="轮播封面图">
          <!-- <el-input v-model="form.phone" style="width: 25%;"></el-input> -->
          <template>
            <!-- <div class="ADDUploadImage_Div" style="border:1px solid red">
                <img src="./../../../../../static/img/img.jpg" />
                <div>
                    <span >*仅支持JPG、PNG格式的图片文件 文件大小小于5M</span>
                    <div>
                        <el-button size="small" class="UploadImage_Button">本地上传</el-button>
                    </div>
                </div>
            </div> -->
            <div class="ADDUploadImage_Div">
                <canvas id="tp" v-if="icontof"></canvas>
                <img v-else id="myicons" style="width:566px;" :src="cover" onerror="this.onerror=null;this.src='./../../../static/img/avatar.jpg'"/>
                <div>
                    <div style="width:230px;height:100px;margin-top:10px;">*仅支持JPG、PNG格式的图片文件 文件大小小于5M</div>
                    <div style="width:230px;height:30px;position: relative;">
                        <el-button size="small" style="width:80px">本地上传</el-button>
                        <input id="myfile" type="file" name="files" multipart class="UIfile" title="点击选择本地文件" @change="getFile($event)" accept="image/jpeg,image/png"/>
                        <div style="display:none">{{qwe}}</div>
                    </div>
                </div>
            </div>
          </template>
        </el-form-item>
        <br/>
        <el-form-item label="课程选择" style="height:40px;">
          <!-- <el-input style="width:295px;height:28px;" :disabled="true" v-model="checkname"></el-input> -->
          <div :title="checkname" style="width:295px;height:32px;border:1px solid #cdcdcd;float:left;text-indent: 5px;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;">{{checkname}}</div>
          <el-button style="width:100px;height:35px;margin-left:5px;" @click="choeseCarousel">选择课程</el-button>
        </el-form-item>
        <br/>
        <el-form-item label="">
            <el-button type="primary" size="large" style="width:100px" @click="Onaddbanner">提 交</el-button>
        </el-form-item>
      </el-form>
      <el-dialog :visible.sync="dialogVisible" size="small" title="课程选择">
        <div style="width:100%;height:300px;border:1px solid #cdcdcd">
            <div class="TopButton2 W95">
                <div class="TopButtonRight2">
                <input class="TopButtonRightInput" type="text" placeholder="请输入关键字" /><label class="el-icon-search TopButtonRightSearch"></label>
                <el-button type="primary" size="small">主要按钮</el-button>
                </div>
            </div>
            <div style="width:96%;height:calc(100% - 70px);margin-top:10px;margin-left:2%">

                <div style="width:100%;height:100%;border:1px solid #cdcdcd;overflow-y: auto">
                    <div style="width:70%;height:100%;float:left;">
                        <div style="width:100%;height:40px;line-height:40px;text-align: center;background:#dcdcdc;border-right:1px solid black;">课程名称</div>
                        <div style="width:100%;height:40px;line-height:40px;text-align: center;border-bottom:1px solid #dcdcdc;border-right:1px solid black;" v-for="data in ResourceData">{{data.name}}</div>
                    </div>
                    <div style="width:calc(30% - 1px);height:100%;float:left;">
                        <div style="width:100%;height:40px;line-height:40px;text-align: center;background:#dcdcdc">选择</div>
                        <div style="width:100%;height:40px;line-height:40px;text-align: center;border-bottom:1px solid #dcdcdc" v-for="data in ResourceData">
                          <input name="Fruit" type="radio" :value="data.id" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="choeseC">提 交</el-button>
        </span>
        </el-dialog>
</div>
</template>

<script>
  import { addbanner,getcourse,uploadImg,getOnecourses,changebanner } from '../../../../api/api';
export default {
  data () {
    return {
      checkid:'',
      checkname:'',
      files: '',
      qwe:'',
      imageUrl: '',
      icontof:false,
      ie9tof:navigator.userAgent.indexOf("MSIE 9.0")>0,
      checkedCities1: [],
      // checkedCities1:[],
        ResourceData:[],
        dialogVisible: false,
        value: '',
        cover:window.localStorage.getItem('saasbannercover'),
        id:window.localStorage.getItem('saasbannerid'),
        val:window.localStorage.getItem('saasbannerval'),
      JUrl:window.localStorage.getItem('saasJUrl')
    }
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }else{
          getOnecourses(window.localStorage.getItem('saasbannerval')).then(data =>{
            if(data.success){
              this.checkname = data.name;
            }else{
              if(data.reqMsg!==undefined&&data.reqMsg!==''){
                this.openError(data.reqMsg);
              }
            }
          })
        }
  },
  methods:{
    Onaddbanner(){
      var that = this;
      // console.log(that.checkid);
      // console.log(that.files);
      if(that.files!==""){
        var options = {
          url:that.JUrl,
          type:'post',
          enctype:'multipart/form-data',
          error:function(){
            that.openError('上传失败');
          },
          success:that.successRes
        }
        that.Jq("#form").ajaxSubmit(options);
      }else{
        if(that.checkid!==""){
            var loginParams = { id:that.id,val: that.checkid};
              changebanner(loginParams).then(data =>{
                if(data.success){
                  that.$router.push('/Carousel');
                }else{
                  that.openError(data.reqMsg);
                }
              })
        }else{
          that.openError('请进行修改');
        }
      }

      // if(that.checkid===""&&this.files!=='123'){
      //   that.openError('请进行修改');
      // }else{
      //   var options = {
      //     url:that.Url,
      //     type:'post',
      //     enctype:'multipart/form-data',
      //     error:function(){
      //       that.openError('上传失败');
      //     },
      //     success:that.successRes
      //   }
      //   that.Jq("#form").ajaxSubmit(options);
      // }
    },
    successRes(jsondata){
      var data = JSON.parse(jsondata);
      if(data.resMsg === "success"){
          var loginParams = '';
              if(this.checkid!==''){
                loginParams = { id:this.id,val: this.checkid, cover: data.url};
              }else{
                loginParams = { id:this.id,val: '', cover: data.url};
              }
              changebanner(loginParams).then(data =>{
                if(data.success){
                  this.$router.push('/Carousel');
                }else{
                  this.openError(data.reqMsg);
                }
              })
          }else{
            this.openError(data.msg);
          }
    },
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    Back:function(){
        this.$router.push('/Carousel');
    },
    choeseCarousel:function(){
      var loginParams = { name: '',pgPageNum:1};
      getcourse(loginParams).then(data =>{
        this.ResourceData = data.resList;
        this.dialogVisible = true;
        // console.log(data);
      });
    },
    getFile(objs) {
      this.files = '123';
      if(this.ie9tof){
        var that = this;
        that.icontof = true;
        setTimeout(function() {
        var obj = document.getElementById("myfile");
        obj.select(); obj.blur();
        that.files = document.selection.createRange().text;
        document.selection.empty();
        document.getElementById("tp").style.filter="progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale',src='"+that.files+"')";
        // console.log(that.files);
        }, 50);
      }else{
        this.icontof = false;
        var myicons = document.getElementById("myicons");
        this.files = objs.target.files[0];
        // console.log(this.files);
        var reader = new FileReader();
        reader.readAsDataURL(this.files);
        reader.onload = function (e) {
            myicons.src = this.result;
        }
      }
    },
    choeseC(){
      var isAutoSend = document.getElementsByName('Fruit');
            for (var i = 0; i < isAutoSend.length; i++) {
                if (isAutoSend[i].checked == true) {
                    this.checkid = isAutoSend[i].value;
                    for(var i=0;i<this.ResourceData.length;i++){
                      if(this.ResourceData[i].id === parseInt(this.checkid)){
                        // console.log(this.ResourceData[i]);
                        this.checkname = this.ResourceData[i].name;
                      }
                    }
                    this.dialogVisible = false;
                }
            }
    }
  }
}
</script>



<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.qwe{
  text-indent: 2
}
</style>



