      successResDialog(data){
        var data = JSON.parse(data);
        this.uploaddata = data;
        // this.dialogVisible3 = true;
        // console.log(data);
        if(data.resMsg === "success"){
          this.loading = false;
          // console.log(data.filename);
          var name = data.filename.split('.');
          // console.log(name);
          if(name.length>1){
            var tname = name[name.length-1];
            // console.log(tname);
            if(tname==='mp3'||tname==='mp4'||tname==='wav'||tname==='mepg'){
              this.radio = '3';
            }else if(tname==='docx'||tname==='ppt'||tname==='txt'||tname==='html'){
              this.radio = '2';
            }else if(tname==='jpg'||tname==='png'||tname==='jepg'||tname==='bmp'||tname==='gif'){
              this.radio = '1';
            }else{
              this.radio = '6';
            }
          }else{
            this.radio = '6';
          }
          // console.log(tname);
          var loginParams = '';
          if(tname==='mp3'){
            loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,audioUrl:this.uploaddata.url,url:this.uploaddata.url};
          }else{
            if(data.Separate!==undefined){
              if(data.Separate==='false'){
                this.openError('分离音频失败');
                loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,url:this.uploaddata.url};
              }else{
                var namelist = this.uploaddata.url.split('.');
                var SeparateName = namelist[0];
                for(var i=1;i<namelist.length-1;i++){
                  SeparateName = SeparateName +'.'+ namelist[i]
                }
                loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,audioUrl:SeparateName+'.aac',url:this.uploaddata.url,min:this.uploaddata.min};
              }
            }else{
                loginParams = { parent: this.filepath[this.filepath.length-1].parent,name:this.uploaddata.filename,type:this.radio,url:this.uploaddata.url};
            }
          }

          addattach(loginParams).then(data =>{
            this.loading = false;
            if(data.success){
              var loginParams = { parent: this.filepath[this.filepath.length-1].parent};
              getattach(loginParams).then(data =>{
                // console.log(data);
                if(data.success){
                // this.dialogVisible3 = false;
                  if(data.resList!==undefined){
                    this.NotDis2 = false;
