<template>
  <div class="BottomDiv">
    <el-col :span="4" class="sidebar">
      <el-menu :default-active="onRoutes" class="el-menu-vertical-demo" unique-opened router style="background:#FFFFFF">
        <template v-for="item in items">
          <el-submenu :index="item.index" :key="item.items">
            <template slot="title">{{ item.title }}</template>
                <el-menu-item v-for="(subItem,i) in item.subs" :key="i" :index="subItem.index" :id="subItem.index" @click="MenuCheck(subItem.index)" active-text-color="#FFFFFF">{{ subItem.title }}
                <div style="display:none">{{qwe}}</div>
            </el-menu-item>
          </el-submenu>
        </template>
      </el-menu>
    </el-col>
    <el-col class="content" :span="20"><router-view></router-view></el-col>
  </div>
</template>

<script>
export default {
  data() {
            return {
                items: [
                    {
                        index: '1',
                        title: '课程中心',
                        subs: [
                            {
                                bgcolor:'#409EFF',
                                index: 'FirstP_List',
                                title: '首页管理'
                            },
                            {
                                bgcolor:'#409EFF',
                                index: 'Resource_List',
                                title: '课程管理'
                            },
                            {
                              bgcolor:'#409EFF',
                              index: 'CourseSort',
                              title: '课程分类'
                            },
                            {
                              bgcolor:'#409EFF',
                              index: 'Teacher_List',
                              title: '讲师管理'
                            }
                        ]
                    },
                    {
                        index: '2',
                        title: '考试中心',
                        subs: [
                          {
                            bgcolor:'#409EFF',
                            index: 'Exam_List',
                            title: '考试管理'
                          },
                          {
                            bgcolor:'#409EFF',
                            index: 'Paper_List',
                            title: '试卷管理'
                          },
                          {
                            bgcolor:'#409EFF',
                            index: 'Test_List',
                            title: '题库管理'
                          },
                          {
                            bgcolor:'#409EFF',
                            index: 'Topic_List',
                            title: '题目管理'
                          }
                        ]
                    },
                    {
                    index: '3',
                    title: '统计中心',
                    subs: [
                      {
                        bgcolor:'#409EFF',
                        index: 'Statistic_List',
                        title: '课程统计'
                      },
                      // {
                      //   bgcolor:'#409EFF',
                      //   index: 'Exam_Statistic',
                      //   title: '人员统计'
                      // }
                    ]
                  }
                ],
                computpath:'',
                qwe:0,
            }
        },
        beforeCreate:function(){
          if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
            this.$router.push('/Login');
          }
        },
        mounted:function(){
            var path = this.$route.path.replace('/','');
            if(path === "AddTeacher" ){
                path = "Teacher_List"
            }
            if(path === "FirstP_List"){
                this.$router.push('/Plate');
            }
            if(path === "Plate" || path === "Carousel" || path === "AddCarousel" || path === "UpdCarousel" ){
                path = "FirstP_List";
            }
            if(path === "Test_Detail"){
                path = "Test_List";
            }
            if(path === "Resource_List"){
                this.$router.push('/Course');
            }
            if(path === "Course" || path === "AddResource" || path === "AddCourseSort" || path === "Chapter" || path === "UpdResource" || path === "UpdCourseSort"){
                path = "Resource_List"
            }
            for(var i in this.items){
                for(var j in this.items[i].subs){
                    if(this.items[i].subs[j].index === path){
                        document.getElementById(this.items[i].subs[j].index).style.backgroundColor = '#409EFF';
                        document.getElementById(this.items[i].subs[j].index).style.color = '#FFFFFF';
                    }else{
                        document.getElementById(this.items[i].subs[j].index).style.backgroundColor = '#FFFFFF';
                        document.getElementById(this.items[i].subs[j].index).style.color = '#48576A';
                    }
                }
            }
            this.computpath = path;
            this.qwe++;
        },
        computed:{
            onRoutes(){
                var path = this.$route.path.replace('/','');
                if(path === "AddTeacher" ){
                    path = "Teacher_List"
                }
                if(path === "FirstP_List"){
                    this.$router.push('/Plate');
                }
                if(path === "Plate" || path === "Carousel" || path === "AddCarousel" || path === "UpdCarousel" ){
                    path = "FirstP_List";
                }
                if(path === "Test_Detail"){
                    path = "Test_List";
                }
                if(path === "Resource_List"){
                    this.$router.push('/Course');
                }
                if(path === "Course" || path === "CourseSort" || path === "AddResource" || path === "AddCourseSort" || path === "Chapter" || path === "UpdResource" || path === "UpdCourseSort"){
                    path = "Resource_List"
                }
                return path;
            }
        },
        methods:{
            MenuCheck(index){
                for(var i in this.items){
                    for(var j in this.items[i].subs){
                        if(this.items[i].subs[j].index === index){
                            document.getElementById(this.items[i].subs[j].index).style.backgroundColor = '#409EFF';
                            document.getElementById(this.items[i].subs[j].index).style.color = '#FFFFFF';
                        }else{
                            document.getElementById(this.items[i].subs[j].index).style.backgroundColor = '#FFFFFF';
                            document.getElementById(this.items[i].subs[j].index).style.color = '#48576A';
                        }
                    }
                }
                this.qwe++;
            }
        }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



