<template>
    <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95">管理员管理</div>
      </div>
      <div class="TopLine"></div>
      <div class="TopButtonT W95">
        <span class="TopButtonLeft">
          <el-button type="primary" @click="addAdmin">添加管理员</el-button>
        </span>
        <div class="TopButtonRight4">
          <input class="TopButtonRightInput" type="text" v-model="mobile" placeholder="请输入手机号码"/><label class="el-icon-search TopButtonRightSearch"></label>
        </div>
      </div>
      <div id="TableDiv" class="W95 Teacher_List_Table">
        <div style="width: 100%;height:calc(100% - 30px);overflow-y: auto;border:1px solid #cdcdcd;"
        v-loading="Gloading"
        element-loading-text="加载中。。。"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)">
          <div style="width:calc(100% - 2px);height:40px;line-height:40px;text-align: center">
            <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">姓名</div>
            <div class='ListBorder'></div>
            <div style="float:left;width:25%;height:100%;border-bottom:1px solid #cdcdcd;">手机号</div>
            <div class='ListBorder'></div>
            <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">状态</div>
            <div class='ListBorder'></div>
            <div style="float:left;width:calc(30% - 4px);height:100%;border-bottom:1px solid #cdcdcd;">最后操作时间</div>
            <div class='ListBorder'></div>
            <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">操作</div>

            <span v-for="data in tableData">
              <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.name}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:25%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.mobile}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">
                <el-switch v-model="data.openOrClose"  active-color="#13ce66" inactive-color="#ff4949" @change="changestatus(data.id,data.status)"></el-switch>
              </div>
              <div class='ListBorder'></div>
              <div style="float:left;width:calc(30% - 4px);height:100%;border-bottom:1px solid #cdcdcd;">{{data.updatetime}}</div>
            <div class='ListBorder'></div>
            <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;"><el-button type="text" @click="OndeleteAdmin(data.id)">删除</el-button></div>
            </span>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import { getaccess,updaccess,addaccess,delaccess } from '../../../api/api';
import { formatDate } from '../../../util/util';
export default {
  data() {
      return {
        tableData: [],
        data: [],
        mobile:'',
        tof:'',
        Gloading:true
      }
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }else{
          getaccess().then(data =>{
            this.tableData = data.resList;
            for(var i=0;i<this.tableData.length;i++){
              var date = new Date(this.tableData[i].updateTime);
              if(this.tableData[i].status===0){
                this.tableData[i].openOrClose = true;
              }else{
                this.tableData[i].openOrClose = false;
              }
              this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
            }
            this.Gloading = false;
          })
        }
    },
    methods:{
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
      changestatus(id,status){
        if(status===1){
          status = 0;
        }else{
          status = 1;
        }
        var loginParams = { id: id,status:status};
        updaccess(loginParams).then(data =>{
          getaccess().then(data =>{
                this.tableData = data.resList;
                for(var i=0;i<this.tableData.length;i++){
                  var date = new Date(this.tableData[i].updateTime);
                  if(this.tableData[i].status===0){
                    this.tableData[i].openOrClose = true;
                  }else{
                    this.tableData[i].openOrClose = false;
                  }
                  this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                }
                this.Gloading = false;
              })
        })
      },
      addAdmin(){
        // if(/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(14[0-9]{1})|)+\d{8})$/.test(this.mobile)){
        if(/^1[3|4|5|6|7|8][0-9]\d{4,8}$/.test(this.mobile)){
          addaccess(this.mobile).then(data =>{
            if(data.success){
              this.openSuccess('添加成功');
              getaccess().then(data =>{
                this.tableData = data.resList;
                for(var i=0;i<this.tableData.length;i++){
                  var date = new Date(this.tableData[i].updateTime);
                  if(this.tableData[i].status===0){
                    this.tableData[i].openOrClose = true;
                  }else{
                    this.tableData[i].openOrClose = false;
                  }
                  this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
                }
                this.Gloading = false;
              })
            }else{
              this.openError(data.resMsg);
            }
          })
        }
        else if(this.mobile==''){
          this.openError('请输入手机号码');
        }
        else{
          this.openError('请输入正确的手机号码');
        }
      },
      OndeleteAdmin(id){
        this.$confirm('此操作将永久删除该管理员, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
              delaccess(id).then(data =>{
                if(data.success){
                  for(var i in this.tableData){
                    if(this.tableData[i].id===id){
                      this.tableData.splice(i,1);
                    }
                  }
                }else{
                  this.openError(data.resMsg);
                }
              })
            }).catch(() => {
        });
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



