<template>
  <div style="width:100%;height:100%;">
    <div>
      <div class="TopName W95">人员管理</div>
    </div>
    <div class="TopLine"></div>
    <div class="TopButtonT W95" style="height: 36px">
      <span class="TopButtonLeft">
        <el-button type="primary" @click="addPersonnel">添加成员</el-button>
      </span>
      <span class="TopButtonRight6">
        <input class="TopButtonRightInput" type="text" v-model="searchTxt" placeholder="请输入关键字" @input="getPersonnel" @keydown="" @keyup.enter="searchUser"/>
        <label class="el-icon-search TopButtonRightSearch6" @click="searchUser"></label>
      </span>
    </div>

    <div id="TableDiv" class="W95 Teacher_List_Table">
      <div style="width: 100%;height:calc(100% - 30px);overflow-y: auto;border:1px solid #cdcdcd;"
           v-loading="Gloading"
           element-loading-text="加载中。。。"
           element-loading-spinner="el-icon-loading"
           element-loading-background="rgba(0, 0, 0, 0.8)">
        <div style="width:calc(100% - 2px);height:40px;line-height:40px;text-align: center">
          <div style="float:left;width:12%;height:100%;border-bottom:1px solid #cdcdcd;">序号</div>
          <div class='ListBorder'></div>
          <div style="float:left;width:12%;height:100%;border-bottom:1px solid #cdcdcd;">姓名</div>
          <div class='ListBorder'></div>
          <div style="float:left;width:14%;height:100%;border-bottom:1px solid #cdcdcd;">手机号</div>
          <div class='ListBorder'></div>
          <div style="float:left;width:calc(50% - 4px);height:100%;border-bottom:1px solid #cdcdcd;">所属部门</div>
          <div class='ListBorder'></div>
          <div style="float:left;width:12%;height:100%;border-bottom:1px solid #cdcdcd;">操作</div>

          <span v-for="data in userInfo.resList">
              <div style="float:left;width:12%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.id}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:12%;height:100%;border-bottom:1px solid #cdcdcd;" class="Over" :title="data.content">{{data.name}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:14%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.mobile}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:calc(50% - 4px);height:100%;overflow: hidden;border-bottom:1px solid #cdcdcd;">
                <span>
                  <span v-for="item in data.deptList">{{item.name}} </span>
                </span>
              </div>
              <div class='ListBorder'></div>
              <div style="float:left;width:12%;height:100%;border-bottom:1px solid #cdcdcd;">
                <el-button type="text" @click="editPersonnel(data)">编辑</el-button>
                <el-button type="text" @click="onDelPersonnel(data)">删除</el-button>
              </div>
            </span>
        </div>
        <not-content v-if="NotDis"></not-content>
      </div>
    </div>

    <el-pagination
      style="margin-right:20px;float:right;"
      :page-size="pgLimit"
      @current-change="handleCurrentChange"
      layout="prev, pager, next, jumper"
      :total="userInfo.pgTotalCount">
    </el-pagination>
  </div>
</template>

<script>
  import { getUserAll,delUser,delUserFromOrg } from '../../../api/api';
  import { formatDate } from '../../../util/util';
  import NotContent from '../Modules/NotContent';

  export default {
    data() {
      return {
        tableData: [],
        data: [],
        mobile:'',
        tof:'',
        Gloading:false,
        NotDis:false,
        searchTxt: '',
        dialogVisible: false,
        titlename:'删除部门成员',
        problem: [],
        userInfo: [],
        deptName:'',
        Page:'',
        pgLimit: 30
      }
    },
    components:{ NotContent },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }
    },
    mounted(){
      var params = {keyword:this.searchTxt,pgPageNum:1,pgLimit:this.pgLimit};
      getUserAll(params).then(data=>{
        if(data.success){
          if(data.resList) this.userInfo = data;
          else this.NotDis = true;
        }
        else this.NotDis = true;
      });
    },
    methods:{
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      addPersonnel(){
        window.localStorage.setItem('saasPrePage','Personnel_List');
        window.localStorage.setItem('saasmember','添加成员');
        window.localStorage.setItem('saasDeptid',null);   // 清空部门管理页面遗留的部门id
        this.$router.push('/AddPersonnel');
      },
      getPersonnel(){
        if(this.searchTxt===''){
          var params = {keyword:this.searchTxt,pgPageNum:1,pgLimit:this.pgLimit};
          getUserAll(params).then(data=>{
            this.userInfo = data;
          });
        }
      },
      searchUser(){
        var params = {keyword:this.searchTxt,pgPageNum:1,pgLimit:this.pgLimit};
        getUserAll(params).then(data=>{
          this.userInfo = data;
        });
      },
      editPersonnel(item){
        var Member = JSON.stringify(item);
        window.localStorage.setItem('saasdataMember',Member);
        window.localStorage.setItem('saasmember','编辑成员');
        window.localStorage.setItem('saasPrePage','Personnel_List');
        if(item.deptList[0].id){ window.localStorage.setItem('saasDeptid',item.deptList[0].id); }
        this.$router.push('/AddPersonnel');
      },
      onDelPersonnel(item){
        this.$confirm('此操作将永久删除该人员, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          delUserFromOrg(item.id).then(data=>{
            if(data.success){
              this.openSuccess('删除成功！');
              if(this.userInfo.resList.length==1){
                this.NotDis = true;
              }
              for(var i in this.userInfo.resList){
                if(this.userInfo.resList[i].id==item.id){
                  this.userInfo.resList.splice(i,1);
                }
              }
            }else{
              this.openError(data.resMsg);
            }
          });
        }).catch(() => {});
      },
      handleCurrentChange(currentPage){
        var params = {keyword:this.searchTxt,pgPageNum:currentPage,pgLimit:this.pgLimit};
        getUserAll(params).then(data=>{
          this.userInfo = data;
        });
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



