<template>
  <div style="width:100%;height:100%;">
    <div>
      <div class="TopName W95">部门管理</div>
    </div>

    <div class="TopLine"></div>
    <div>
      <div class="TopName W95">
        <span>
          <el-button type="primary" @click="addDepartOne">添加同级部门</el-button>
        </span>
        <span class="TopButtonLeft_1">
          <el-button type="primary" @click="addDepart" :disabled="btnDisabledChild">添加子部门</el-button>
        </span>
        <span class="TopButtonLeft_1">
          <el-button type="primary" @click="EditDepartment" :disabled="btnDisabled">编辑部门</el-button>
        </span>
        <span class="TopButtonLeft_1">
          <el-button type="primary" @click="onDelDepartment" :disabled="btnDisabled">删除部门</el-button>
        </span>

        <span class="SearchDept TopButtonRight6">
          <input v-model="searchTxt" type="text" placeholder="请输入部门关键字" @keyup.enter="searchDept">
          <el-button type="default" style="padding: 5px 10px;" @click="searchDept">搜索</el-button>
        </span>
      </div>
    </div>

    <div class="TopContent W95" style="border: 1px solid #cdcdcd;">
      <div class="TopContentLeft" id="DeptTopLength_1">
        <el-tree
          :data="DepartmentList"
          ref="tree"
          node-key="id"
          empty-text="暂无数据"
          accordion
          :indent="20"
          :highlight-current="true"
          :props="defaultProps"
          :current-node-key="highlightkeys"
          @node-click="handleNodeClick"
        ></el-tree>
      </div>

      <div class="TopContentRight">
        <div class="TopContentRight1">
          <span v-if="DeptUsersNum">{{currentDept||'部门名称'}}({{DeptUsersNum}}人)</span>
          <span v-else>{{currentDept||'部门名称'}}(0人)</span>
          <span>
            <div style="width:100%;height:25px;">
              <div style="width:100px;height:25px;float:left;text-align: center;">
                <el-button size="small" @click="addDeptMember" :disabled="btnDisabled" style="width:100px">添加部门成员</el-button>
              </div>
            </div>
          </span>
        </div>
        <div class="TopContentRight2">
          <div class="Department_List_Table">
            <div style="width: 100%;height:calc(100% - 20px);overflow-y: auto;border:1px solid #cdcdcd;"
                 element-loading-text="加载中。。。"
                 element-loading-spinner="el-icon-loading"
                 element-loading-background="rgba(0, 0, 0, 0.8)">
              <div style="height:40px;line-height:40px;text-align: center">
                <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">序号</div>
                <div class='ListBorder'></div>
                <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">姓名</div>
                <div class='ListBorder'></div>
                <div style="float:left;width:20%;height:100%;border-bottom:1px solid #cdcdcd;">手机号</div>
                <div class='ListBorder'></div>
                <div style="float:left;width:30%;height:100%;border-bottom:1px solid #cdcdcd;">部门</div>
                <div class='ListBorder'></div>
                <div style="float:left;width:calc(20% - 4px);height:100%;border-bottom:1px solid #cdcdcd;">操作</div>

                <span v-for="data in userInfo.resList" v-if="hackReset">
                  <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.id}}</div>
                  <div class='ListBorder'></div>
                  <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;" class="Over" :title="data.content">{{data.name}}</div>
                  <div class='ListBorder'></div>
                  <div style="float:left;width:20%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.mobile}}</div>
                  <div class='ListBorder'></div>
                  <div style="float:left;width:30%;height:100%;border-bottom:1px solid #cdcdcd;">{{currentDept||'一级部门'}}</div>
                  <div class='ListBorder'></div>
                  <div style="float:left;width:calc(20% - 4px);height:100%;border-bottom:1px solid #cdcdcd;">
                    <el-button type="text" @click="editPersonnel(data)">编辑</el-button>
                    <el-button type="text" @click="onDelPersonnel(data)">移除</el-button>
                  </div>
                </span>
              </div>
              <not-content v-if="NotDis"></not-content>
            </div>
          </div>
        </div>
        <div class="TopContentRight3">
          <el-pagination
            style="float:right;"
            :page-size="pgLimit"
            @current-change="handleCurrentChange"
            layout="prev, pager, next, jumper"
            :total="userInfo.pgTotalCount">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { getDepartmentAll,getDepartmentById,getUserBy,delDepartment,delUser } from '../../../api/api';
  import NotContent from '../Modules/NotContent';

  export default {
    data() {
      return {
        item: [],
        userInfo: [],
        tof:'',
        NotDis:true,
        searchTxt: '',
        currentIndex: '',
        currentParent: '',
        currentDept: '部门名称',
        currentDeptParent: '',
        hackReset:false,
        btnDisabled: true,
        btnDisabledChild: true,
        pgLimit: 12,
        DeptUsersNum: 0,
        defaultProps: {
          children: 'resList',
          label: 'name'
        },
        highlightkeys: 0
      }
    },
    components:{ NotContent },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }
    },
    computed:{
      onRoutes(){
        var path = this.$route.path.replace('/','');
        return path;
      },
      DepartmentList(){
        if(this.$store.state.DepartmentList.length!=0){
          this.NotDis=false;
          return this.$store.state.DepartmentList;
        }
        else {
          getDepartmentAll().then(res=>{
            if (res.resCode == 0) {
              this.$store.commit('updateDeptList', res.resList)
            }
          })
        }
      }
    },
    methods:{
      getuser(params){
        this.userInfo = [];
        getUserBy(params).then(data=>{
          // console.log(data);
          this.userInfo = data;
          if(data.resList) this.DeptUsersNum = data.resList.length;
          else this.DeptUsersNum = 0;
        });
      },
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      handleNodeClick(item) {
        this.searchTxt = '';
        var params = {deptid:item.id,keyword:this.searchTxt,pgPageNum:1};
        this.getuser(params);
        this.currentIndex = parseInt(item.id);
        this.currentParent = parseInt(item.parent);

        getDepartmentById(this.currentIndex).then(data=>{
          if(data.success){
            window.localStorage.setItem('saasDeptidPre',data.parent);
          }
        });

        this.currentDept = item.name;
        this.btnDisabled = false;
        this.btnDisabledChild= false;

        this.hack();
      },
      addDepartOne(){
        window.localStorage.setItem('saasdept','添加部门');
        getDepartmentById(this.currentIndex).then(data=>{
          if(data.success){
            window.localStorage.setItem('saasDeptidPre',data.parent);
          }
        });
        if(this.currentIndex!=''){
          window.localStorage.setItem('saasdeptType','1');
        }
        this.$router.push('/AddDepartment');
      },
      addDepart(){
        window.localStorage.setItem('saasdept','添加部门');
        window.localStorage.setItem('saasDeptidPre',this.currentIndex);
        if(this.currentIndex!=''){
          window.localStorage.setItem('saasdeptType','1');
        }
        this.$router.push('/AddDepartment');
      },
      EditDepartment(){
        window.localStorage.setItem('saasdept','编辑部门');
        window.localStorage.setItem('saasDeptid',this.currentIndex);
        this.$router.push('/AddDepartment');
      },
      addDeptMember(){
        window.localStorage.setItem('saasPrePage','Department_List');
        window.localStorage.setItem('saasDeptid',this.currentIndex);
        window.localStorage.setItem('saasmember','添加成员');
        this.$router.push('/AddPersonnel');
      },
      editPersonnel(item){
        var Member = JSON.stringify(item);
        window.localStorage.setItem('saasmember','编辑成员');
        window.localStorage.setItem('saasDeptid',this.userInfo.deptid);
        window.localStorage.setItem('saasdataMember',Member);
        window.localStorage.setItem('saasPrePage','Department_List');
        this.$router.push('/AddPersonnel');
      },
      onDelDepartment(){
        this.$confirm('此操作将永久删除该部门, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          delDepartment(this.currentIndex).then(data=>{
            if(data.success){
              this.openSuccess(data.resMsg);
              getDepartmentAll(window.localStorage.getItem("saasoid")).then(data=>{
                this.$store.commit("updateDeptList", data.resList);
              });
              this.btnDisabled = true;
              this.btnDisabledChild = true;
              this.currentDept = '部门名称';
              this.userInfo.resList = [];
            }else{
              this.openError(data.resMsg);
            }
          });
        }).catch(() => {});
      },
      onDelPersonnel(item){
        this.$confirm('此操作将永久删除该人员, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          delUser(this.currentIndex,item.id).then(data=>{
            if(data.success){
              this.openSuccess(item.name+'已经从'+this.currentDept+'移除！');

              if(this.userInfo.resList.length==1){
                this.NotDis = true;
              }
              for(var i in this.userInfo.resList){
                if(this.userInfo.resList[i].id==item.id){
                  this.userInfo.resList.splice(i,1);
                }
              }
            }else{
              this.openError(data.resMsg);
            }
          });
        }).catch(() => {});
      },
      hack(){
        this.hackReset = false; // hack方法强制刷新组件
        this.$nextTick(() => {
          this.hackReset = true;
        })
      },
      getUsers(){
        if(this.searchTxt===''){
          var params = {deptid:this.currentIndex,pgPageNum:1,pgLimit:this.pgLimit,keyword:this.searchTxt};
          this.getuser(params);
        }
      },
      searchUser(){
        if(this.searchTxt!=''){
          var params = {deptid:this.currentIndex,pgPageNum:1,pgLimit:this.pgLimit,keyword:this.searchTxt};
          this.getuser(params);
        }
        else this.openError('请先输入相关部门名称再进行搜索操作！');
      },
      reverseDept(item){
        if(this.$refs.tree.store.nodesMap[item.parent] != undefined){
          this.$refs.tree.store.nodesMap[item.parent].expanded = true;
          if(this.$refs.tree.store.nodesMap[item.parent].data.parent != 0){
            this.reverseDept(this.$refs.tree.store.nodesMap[item.parent].data);
          }
          else{
            return;
          }
        }
      },
      recursiveDept(data){
        if(data.resList!=''){
          for(var i=0;i<data.resList.length;i++){
            var item = data.resList[i];
            var result = item.name.match(this.searchTxt);
            if(result != null){
              this.highlightkeys = item.id;
              this.reverseDept(item);     // 循环展开上级部门
              this.getuser({deptid:item.id});
              this.currentIndex = parseInt(item.id);
              this.currentParent = parseInt(item.parent);

              getDepartmentById(item.id).then(data=>{
                if(data.success){
                  window.localStorage.setItem('saasDeptidPre',data.parent);
                }
              });

              this.currentDept = item.name;
              this.btnDisabled = false;
              this.btnDisabledChild= false;
              this.hack();
            }
            else{
              this.recursiveDept(item);
            }
          };
        }
        else{
          return;
        }
      },
      searchDept(){
        if(this.searchTxt!=''){
          for(var i=0;i<this.DepartmentList.length;i++){
            var item = this.DepartmentList[i];
            var result = item.name.match(this.searchTxt);
            if(result != null){
              this.highlightkeys = item.id;
              this.$refs.tree.store.nodesMap[item.id].expanded = true;
              this.getuser({deptid:item.id});
              this.currentIndex = parseInt(item.id);
              this.currentParent = parseInt(item.parent);

              getDepartmentById(this.currentIndex).then(data=>{
                if(data.success){
                  window.localStorage.setItem('saasDeptidPre',data.parent);
                }
              });

              this.currentDept = item.name;
              this.btnDisabled = false;
              this.btnDisabledChild= false;
              this.hack();
            }
            else {
              this.recursiveDept(item);
            }
          };
        }
        else{
          this.openError('请先输入部门关键字再搜索！');
        }
      },
      searchDept2(){
        for(var i=0;i<this.DepartmentList.length;i++){
          var item = this.DepartmentList[i];
          var result = item.name.match(this.searchTxt);
          if(result != null && result[0] != ''){
            this.getuser({deptid:item.id});
            this.currentIndex = parseInt(item.id);
            this.currentParent = parseInt(item.parent);

            getDepartmentById(this.currentIndex).then(data=>{
              if(data.success){
                window.localStorage.setItem('saasDeptidPre',data.parent);
              }
            });

            this.currentDept = item.name;
            this.btnDisabled = false;
            this.btnDisabledChild= false;

            this.$refs.tree.setCheckedNodes([{
              id: item.id,
              label: item.name
            }]);

            this.currentDept = item.name;
            document.getElementById('DeptTopLength_1').scrollTop = 50 * i;
            this.hack();
            return;
          }
        };
      },
      show(ev){
        if(ev.keyCode===13){
          this.searchDept();
        }
      },
      handleCurrentChange(currentPage){
        var params = {deptid:this.currentIndex,pgPageNum:currentPage,pgLimit:this.pgLimit,keyword:this.searchTxt};
        getUserBy(params).then(data=>{
          this.userInfo = data;
        });
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



