
<template>
  <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95"><i class="el-icon-close" style="margin-right:10px;cursor: pointer;" @click="GoCourseSort"></i>添加分类</div>
      </div>
      <div class="TopLine"></div>
        <br/>
        <br/>
        <br/>
      <el-form ref="form" :model="form" label-width="150px" >
        <el-form-item label="分类名称">
          <el-input v-model="form.name" style="width: 400px;" @change="verification"></el-input>
        </el-form-item>
        <br/>
        <el-form-item label="分类描述" style="width: 550px;">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入内容" @change="verification"
            v-model="form.desc">
          </el-input>
        </el-form-item>
        <br/>
        <el-form-item label="所属上级分类" style="height:40px;">
          <el-input style="width:400px;height:28px;" :disabled="true" v-model="CnameBlock" @change="verification"></el-input>
          <!-- <el-button style="width:100px;height:35px;"  @click="ChoeseC">选择分类</el-button> -->
        </el-form-item>
        <br/>
        <el-form-item label="" >
          <el-button type="primary"style="width:120px" :disabled="tof" @click="Onaddcategory">提交</el-button>
        </el-form-item>
      </el-form>
    <el-dialog :visible.sync="dialogVisible" size="small" title="选择分类">
        <div style="width:100%;height:300px;overflow-y: auto;border:1px solid #cdcdcd">
              <div style="width:100%;" v-for="One in data">
                <div style="width:100%;line-height:40px;text-indent: 5px;cursor: pointer;" v-bind:class="{ asd: One.rofShadow }" @click="TreeClick(One.id,One.name,One.desc)">
                  <span v-if="One.resList!==undefined">
                    <i v-if="One.tofDis" class="el-icon-minus"></i>
                    <i v-else class="el-icon-plus"></i>
                  </span>
                  {{One.name}}
                </div>
                <div style="width:100%;" v-if="One.tofDis" v-for="Two in One.resList">
                  <div style="width:100%;height:40px;line-height:40px;text-indent: 15px;cursor: pointer;" v-bind:class="{ asd: Two.rofShadow }" @click="TreeClick(Two.id,Two.name,Two.desc)">
                    <span v-if="Two.resList!==undefined">
                      <i v-if="Two.tofDis" class="el-icon-minus"></i>
                      <i v-else class="el-icon-plus"></i>
                    </span>
                    {{Two.name}}
                  </div>
                  <div style="width:100%;" v-if="Two.tofDis" v-for="Three in Two.resList">
                    <div style="width:100%;height:40px;line-height:40px;text-indent: 60px;" v-bind:class="{ asd: Three.rofShadow }" @click="TreeClick(Three.id,Three.name,Three.desc)">{{Three.name}}</div>
                  </div>
                </div><div style="display:none">{{qwe}}</div>
              </div>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="choeseTrue">提 交</el-button>
        </span>
      </el-dialog>
</div>
</template>

<script>
  import { getcategory,getcourseCategory,addcategory,getOnecategory,getOnecourse } from '../../../api/api';
export default {
  data () {
    return {
      form: {
        name: '',
        desc: ''
      },
      radio: '1',
      dialogVisible: false,
      dialogVisible2:false,
      Cid:'',
      Cname:'',
      CidBlock:window.localStorage.getItem('saascategoryid'),
      CnameBlock:window.localStorage.getItem('saascategoryname'),
      data: [],
      dataL: [],
      tof:true,
      qwe:0,
    }
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }
  },
  methods:{
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    GoCourseSort(){
       this.$router.push('/CourseSort');
    },
    ChoeseC(){
      this.dialogVisible = true;
      getcategory().then(data =>{
        // console.log(data);
        if(data.success){
          if(data.resList!==undefined){
            this.data = data.resList;
            for(var i=0;i<this.data.length;i++){
              if(this.data[i].resList!==undefined){
                this.data[i].tofDis = false;
                for(var j=0;j<this.data[i].resList.length;j++){
                  if(this.data[i].resList[j].resList!==undefined){
                    this.data[i].resList[j].tofDis = false;
                  }
                }
              }
            }
            for(var i=0;i<this.data.length;i++){
              if(this.data[i].resList!==undefined){
                this.data[i].rofShadow = false;
                for(var j=0;j<this.data[i].resList.length;j++){
                  if(this.data[i].resList[j].resList!==undefined){
                    this.data[i].resList[j].rofShadow = false;
                    for(var k=0;k<this.data[i].resList[j].resList.length;k++){
                      this.data[i].resList[j].resList[k].rofShadow = false;
                    }
                  }
                }
              }
            }
          }
        }else{
          this.openError(data.resMsg);
        }
      })
    },
    ChoeseL(){
      this.dialogVisible2 = true;
      if(this.dataL.length===0){
        getcourseCategory().then(data =>{
          // console.log(data.resList);
          this.dataL = data.resList;
          if(data.resList!==undefined){
            for(var i=0;i<this.dataL.length;i++){
              this.dataL[i].tofShadow = false;
            }
          }
        });
      }
    },
    verification(){
      if(this.form.name!==''&&this.form.desc!=''&&this.CidBlock!==''&&this.LidBlock!==''){
        this.tof = false;
      }else{
        this.tof = true;
      }

    },
    getcc(id){
      // console.log(id);
      getcourseCategory(id).then(data =>{
        // console.log(data.resList);
        this.dataL = data.resList;
        if(data.resList!==undefined){
          for(var i=0;i<this.dataL.length;i++){
             this.dataL[i].tofShadow = false;
          }
        }
      });
    },
      // },
    choeseTrue(){
      this.dialogVisible = false;
      this.CnameBlock = this.Cname;
      this.CidBlock = this.Cid;
    },
    TreeClick(id,name,desc){
      this.Cname = name;
      this.Cid = id;
        for(var i=0;i<this.data.length;i++){
          if(this.data[i].id===id){
            if(this.data[i].tofDis){
              this.data[i].tofDis = false;
            }else{
              this.data[i].tofDis = true;
            }
            break;
          }else{
            for(var j=0;j<this.data[i].resList.length;j++){
              if(this.data[i].resList[j].id===id){
                if(this.data[i].resList[j].tofDis){
                  this.data[i].resList[j].tofDis = false;
                }else{
                  this.data[i].resList[j].tofDis = true;
                }
                break;
              }
            }
          }
        }

        for(var i=0;i<this.data.length;i++){
          if(this.data[i].resList!==undefined){
            if(this.data[i].id===id){
              if(this.data[i].rofShadow){
                this.data[i].rofShadow = false;
              }else{
                this.data[i].rofShadow = true;
              }
            }else{
                this.data[i].rofShadow = false;
            }
            for(var j=0;j<this.data[i].resList.length;j++){
              if(this.data[i].resList[j].resList!==undefined){
                if(this.data[i].resList[j].id===id){
                  if(this.data[i].resList[j].rofShadow){
                    this.data[i].resList[j].rofShadow = false;
                  }else{
                    this.data[i].resList[j].rofShadow = true;
                  }
                }else{
                  this.data[i].resList[j].rofShadow = false;
                }
                for(var k=0;k<this.data[i].resList[j].resList.length;k++){
                  if(this.data[i].resList[j].resList[k].id===id){
                    if(this.data[i].resList[j].resList[k].rofShadow){
                      this.data[i].resList[j].resList[k].rofShadow = false;
                    }else{
                      this.data[i].resList[j].resList[k].rofShadow = true;
                    }
                  }else{
                    this.data[i].resList[j].resList[k].rofShadow = false;
                  }
                }
              }
            }
          }
        }
        this.qwe++;
      },
      Onaddcategory(){
        this.form.name!==''&&this.form.desc!=''&&this.CidBlock!==''&&this.LidBlock!==''
        var loginParams = { name: this.form.name,desc:this.form.desc,course:'',parent:this.CidBlock};
        addcategory(loginParams).then(data =>{
          if(data.success){
            this.$router.push('/CourseSort');
          }else{
            this.openError(data.resMsg);
          }
        })
      }
  }
}
</script>
<style>
.asd{
  background: #dcdcdc;
}
</style>



