<template>
  <div style="margin:20px">
    <form  id="form" class="UploadImage_Div">
        <canvas id="tp" v-if="icontof"></canvas>
        <img v-else id="myicons" :src="myicons" onerror="this.onerror=null;this.src='./../../../static/img/avatar.jpg'"/>
        <!-- <img src="./../../../static/img/avatar.jpg"/> -->
        <div>
            <div style="width:230px;height:100px;margin-top:10px;padding-left: 20px;">*仅支持JPG、PNG格式的图片文件 文件大小小于5M</div>
            <div style="width:230px;height:30px;padding-left: 20px;position: relative;">
                <el-button size="small" style="margin-left:20px;width:80px">本地上传</el-button>
                <input id="myfile" type="file" name="files" multipart class="UIfile" title="点击选择本地文件" @change="getFile($event)" accept="image/jpeg,image/png"/>
                <div style="display:none">{{qwe}}</div>
            </div>
        </div>
    <el-button type="primary" style="margin:20px;margin-top:30px;width:80px;" @click="OnuploadImg()">保存</el-button>
    <!-- <input type="submit" value="提交"/> -->
		</form>
  </div>
</template>
<script>
import { uploadImg,updUserInfoImg,uploadImgFile,qwer } from '../../../api/api';
export default {
  data () {
    return {
      myicons: window.localStorage.getItem("saaslogoUrl"),
      files: '',
      qwe:'',
      imageUrl: '',
      icontof:false,
      ie9tof:navigator.userAgent.indexOf("MSIE 9.0")>0,
      JUrl:window.localStorage.getItem('saasJUrl')
    }
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }
  },
  methods:{
    OnuploadImg(){
      var that = this;
      if(that.files===''){
        that.openError('请选择图片');
      }else{
        var options = {
          url:that.JUrl,
          // dataType:"text/html",
          type:'post',
          // complete:showResponse,
          // clearForm:false,
          // Accept:'*/*',
          // dataType:'text',
          // contentType:'text/html',
          // headers: "Access-Control-Allow-Origin",
          // enctype:'multipart/form-data',
          success:that.successRes,
          error:function(XmlHttpRequest, textStatus, errorThrown){
            // console.log("error:"+XmlHttpRequest);
            // console.log("error:"+errorThrown);
          }
        }
        // console.log(options);
        that.Jq("#form").ajaxSubmit(options);
        return false;
      }
    },
    successRes(data){
        var data = JSON.parse(data);
      // var data = JSON.parse(jsondata);
      // console.log(data);
      if(data.resMsg === "success"){
        window.localStorage.setItem("saaslogoUrl",data.url);
        updUserInfoImg().then(data =>{
          // console.log(JSON.stringify(data));
          if(data.success){
            this.openSuccess("头像上传成功");
            window.location.reload();
            this.qwe++;
          }else{
            this.openError(data.reqMsg);
          }
        });
      }else{
        this.openError(data.msg);
      }
    },
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    getFile(objs) {
      this.files = '123';
      // console.log(this.files);
      if(this.ie9tof){
        this.icontof = true;
        this.qwe++;
        setTimeout(function() {
        var obj = document.getElementById("myfile");
        obj.select(); obj.blur();
        this.files = document.selection.createRange().text;
        // console.log(this.files);
        document.selection.empty();
        document.getElementById("tp").style.filter="progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale',src='"+this.files+"')";
        }, 100);
      }else{
        this.icontof = false;
        var myicons = document.getElementById("myicons");
        this.files = objs.target.files[0];
        // console.log(this.files);
        var reader = new FileReader();
        reader.readAsDataURL(this.files);
        reader.onload = function (e) {
            myicons.src = this.result;
        }
      }
    },
  }
}

</script>


<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>



