
<template>
  <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95"><i class="el-icon-close" style="margin-right:10px;cursor: pointer;" @click="Back"></i>{{title}}</div>
      </div>
      <div class="TopLine"></div>
        <br/>
      <el-form ref="form"  label-width="100px" style="height:calc(100% - 80px);overflow-y: auto;" id="form" method="post">
        <el-form-item label="讲师姓名">
          <el-input v-model="name" style="width: 25%;" @change="verification" :disabled="disN"></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="mobile" style="width: 25%;" @change="verifications" :disabled="this.title==='修改信息'" ></el-input>
        </el-form-item>
        <el-form-item label="头像Logo">
          <div class="UploadImage_Div">
              <canvas id="tp" v-if="icontof"></canvas>
              <img v-else id="myicons" :src="cover" style="object-fit:cover;"/>
              <div>
                  <div style="width:230px;height:100px;margin-top:10px;padding-left: 20px;">*仅支持JPG、PNG格式的图片文件 文件大小小于5M</div>
                  <div style="width:230px;height:30px;padding-left: 20px;position: relative;">
                      <el-button size="small" style="width:80px">本地上传</el-button>
                      <input id="myfile" type="file" name="files" multipart style="margin-left: 20px;" class="UIfile" title="点击选择本地文件" @change="getFile($event)" accept="image/jpeg,image/png"/>
                      <div style="display:none">{{qwe}}</div>
                  </div>
              </div>
          </div>
        </el-form-item>
        <el-form-item label="讲师简介" style="width: 485px;">
          <el-input
            type="textarea"
            :rows="4"
            placeholder="请输入内容"
            v-model="desc" @change="verification">
          </el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-button v-if="dis" type="primary"style="width:120px" @click="Onaddlecturer" :disabled="tof">提交</el-button>
          <el-button v-else type="primary"style="width:120px" @click="Onupdlecturer" :disabled="tof">提交</el-button>
        </el-form-item>
      </el-form>
</div>
</template>

<script>
import { addlecturer,uploadImg,updLecturerInfo,getOneUserInfo } from '../../../api/api';
export default {
  data () {
    return {
      myicons:'',
      files: '',
      qwe:'',
      imageUrl: '',
      icontof:true,
      ie9tof:navigator.userAgent.indexOf("MSIE 9.0")>0,
      name: '',
      mobile: '',
      desc: '',
      cover:'',
      tof:true,
      dis:true,
      disN:false,
      title:window.localStorage.getItem('saaslecture'),
      JUrl:window.localStorage.getItem('saasJUrl')
    }
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }
  },
  mounted:function(){
    if(window.localStorage.getItem('saaslecture')==='修改信息'){
      var Lecturer = JSON.parse(window.localStorage.getItem('saasdata'));
      // console.log(Lecturer);
      this.name = Lecturer.name;
      this.mobile = Lecturer.mobile;
      this.desc = Lecturer.desc;
      this.cover = Lecturer.cover;
      this.dis = false;
      this.icontof = false;
    }
  },
  methods:{
    Onaddlecturer(){
      // console.log(this.files);
      if(this.files===""){
        if(this.cover==='./../../../../static/img/avatar.jpg'){
          var loginParams = { mobile: this.mobile,cover:'',name:this.name,desc:this.desc};
        }else{
          var loginParams = { mobile: this.mobile,cover:this.cover,name:this.name,desc:this.desc};
        }

        // console.log(loginParams);
        addlecturer(loginParams).then(data =>{
          // console.log(data);
          if(data.success){
            this.files = '';
            this.$router.push('/Teacher_List');
          }else{
            this.openError(data.resMsg);
          }
        })
      }else{
        var that = this;
        // console.log(that.Jurl);
        var options = {
          url:that.JUrl,
          type:'post',
          enctype:'multipart/form-data',
          error:function(){
            that.openError('上传失败');
          },
          success:that.successRes
        }
        that.Jq("#form").ajaxSubmit(options);
      }
    },
    successRes(jsondata){
      var data = JSON.parse(jsondata);
      if(data.resMsg === "success"){
         var loginParams = { mobile: this.mobile,cover:data.url,name:this.name,desc:this.desc};
              // console.log(loginParams);
              addlecturer(loginParams).then(data =>{
                // console.log(data);
                if(data.success){
                  this.files = '';
                  this.$router.push('/Teacher_List');
                }else{
                  this.openError(data.resMsg)
                }
              })
          }else{
            this.openError(data.msg);
          }
    },
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    Back:function(){
        this.$router.push('/Teacher_List');
    },
    Onupdlecturer(){
        if(this.files===""){
        var Lecturer = JSON.parse(window.localStorage.getItem('saasdata'));
          var loginParam = {id:Lecturer.id,desc:this.desc,name:this.name,mobile:this.mobile};
            updLecturerInfo(loginParam).then(data =>{
              if(data.success){
                this.$router.push('/Teacher_List');
                this.openSuccess('信息修改成功');
              }else{
                this.openError(data.resMsg);
              }
            })
        }else{
        var that = this;
        // console.log(that.JUrl);
        var options = {
          url:that.JUrl,
          type:'post',
          enctype:'multipart/form-data',
          error:function(){
            that.openError('上传失败');
          },
          success:that.UsuccessRes
        }
        this.Jq("#form").ajaxSubmit(options);
      }

    },
    UsuccessRes(jsondata){
      var data = JSON.parse(jsondata);
      if(data.resMsg === "success"){
        var Lecturer = JSON.parse(window.localStorage.getItem('saasdata'));
        //  var loginParams = { mobile: this.mobile,cover:data.url,name:this.name,desc:this.desc};
          var loginParam = {id:Lecturer.id,desc:this.desc,cover:data.url,name:this.name,mobile:this.mobile};
            updLecturerInfo(loginParam).then(data =>{
              if(data.success){
                this.$router.push('/Teacher_List');
                this.openSuccess('信息修改成功');
              }else{
                this.openError(data.resMsg);
              }
            })
          }else{
            this.openError(data.msg);
          }
    },
      getFile(objs) {
      this.files = '123';
        if(this.ie9tof){
          this.icontof = true;
          var obj = document.getElementById("myfile");
          obj.select(); obj.blur();
          this.files = document.selection.createRange().text;
          document.selection.empty();
          document.getElementById("tp").style.filter="progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale',src='"+this.files+"')";
        }else{
          var that = this;
          that.icontof = false;
          setTimeout(function() {
          var myicons = document.getElementById("myicons");
          that.files = objs.target.files[0];
          // console.log(that.files);
          var reader = new FileReader();
          reader.readAsDataURL(that.files);
          reader.onload = function (e) {
              myicons.src = this.result;
          }
          },10);
        }
      },
    verification(){
      if(this.name!==''){
        this.tof = false;
      }else{
        this.tof = true;
      }
    },
    verifications(){
      if(this.mobile!==''&&/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(14[0-9]{1})|)+\d{11})$/.test(this.mobile)){
        if(this.name!==''){
          this.tof = false;
        }else{
          this.tof = true;
        }
        getOneUserInfo(this.mobile).then(data =>{
          // console.log(data);
          if(data.success){
            this.name = data.name;
            this.desc = data.desc;
            this.cover = data.cover;
            if(this.cover!==undefined){
              this.icontof = false;
            }
          }
        })
      }else if(this.mobile===''&&this.name!==''){
        this.tof = false;
      }else{
        this.tof = true;
      }
    }
  }
}
</script>



