<template>
    <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95">讲师管理</div>
      </div>
      <div class="TopLine"></div>
      <div class="TopButton W95">
        <span class="TopButtonLeft">
          <el-button type="primary" @click="addteacher">添加讲师</el-button>
        </span>
        <div class="TopButtonRight">
        </div>
      </div>
      <div id="TableDiv" class="W95 Teacher_List_Table" style="overflow-y: auto;"
        v-loading="Gloading"
        element-loading-text="加载中。。。"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)">
        <div class="Teacher_list_div" v-for="data in tableData">
          <div style="width:100%;height:60px;cursor: pointer;" @click="updteacher(data)">
            <img onerror="this.onerror=null;this.src='./../../../static/img/avatar.jpg'" :src="data.cover" style="width:60px;height:60px;float:left;margin-right:20px;object-fit:cover;"/>
            <div style="width:150px;height:30px;line-height:30px;float:left;font-size:16px;color:#333333;">{{data.name}}</div>
            <div style="width:150px;height:30px;line-height:30px;float:left;color:#333333;">{{data.mobile}}</div>
          </div>
          <div style="width:100%;height:40px;padding-left:10px;margin-top:10px;">
            <div style="width:50%;height:40px;float:left;line-height:40px;">课程：{{data.courseCount}}</div>
            <div style="width:50%;height:40px;float:left;line-height:40px;">
              <el-button type="text" @click="GoAddResource(data)">添加课程</el-button>
              <el-button type="text" @click="Ondellecturer(data.id,data.uid)">删除</el-button>
            </div>
          </div>
          <!--<div style="width:100%;height:40px">-->
            <!--<div style="width:50%;height:40px;float:left;text-align: center"><el-button size="small" @click="updteacher(data)" style="width:80px">编辑</el-button></div>-->
            <!--<div style="width:50%;height:40px;float:left;"><el-button size="small" style="width:80px" @click="Ondellecturer(data.id,data.uid)">删除</el-button></div>-->
          <!--</div>-->
        </div>
        <not-content v-if="NotDis"></not-content>
      </div>
    </div>
</template>

<script>
  import { getlecturer,dellecturer } from '../../../api/api';
  import NotContent from '../Modules/NotContent';
export default {
  data() {
      return {
        Gloading:true,
        NotDis:false,
        tableData: []
      }
    },
    components:{
      NotContent
    },
    methods:{
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      addteacher:function(){
        window.localStorage.setItem('saaslecture','添加教师');
        this.$router.push('/AddTeacher');
      },
      updteacher(data){
        var Lecturer = JSON.stringify(data);
        window.localStorage.setItem('saasdata',Lecturer);
        window.localStorage.setItem('saaslecture','修改信息');
        this.$router.push('/AddTeacher');
      },
      GoAddResource:function(data){
        window.localStorage.setItem('saasteacherid', data.id);
        window.localStorage.setItem('saasteachername', data.name);
        this.$router.push('/AddResource');
      },
      Ondellecturer(id,uid){
        this.$confirm('此操作将永久删除该讲师, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
              var loginParams = { id:id,uid:uid};
                dellecturer(loginParams).then(data =>{
                  if(data.success){
                    if(this.tableData.length===1){
                      this.NotDis = true;
                    }
                    for(var i in this.tableData){
                      if(this.tableData[i].id===id){
                        this.tableData.splice(i,1);
                      }
                    }
                  }else{
                    this.openError(data.resMsg);
                  }
                })
            }).catch(() => {});
      }
    },
    beforeCreate(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }else{
          getlecturer().then(data =>{
            // console.log(data);
            this.tableData = data.resList;
            if(this.tableData!==undefined){
              this.NotDis = false;
              for(var i=0;i<this.tableData.length;i++){
                if(this.tableData[i].cover===undefined){
                  this.tableData[i].cover = './../../../static/img/avatar.jpg'
                }
              }
            }else{
              this.NotDis = true;
            }
            this.Gloading = false;
          });
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.Teacher_list_div{
  border-radius: 4px;
  width:240px;
  /*height:130px;*/
  border: 1px solid #cdcdcd;
  float: left;
  margin-right: 20px;
  margin-top: 10px;
  padding: 10px;

}
</style>



