<template>
    <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95">题库管理</div>
      </div>
      <div class="TopLine"></div>
      <div class="TopButtonT W95">
        <span class="TopButtonLeft">
          <el-button type="primary" @click="addhomeclick" style="width:100px">添加题库</el-button>
        </span>
        <div class="TopButtonRight4">
          <input class="TopButtonRightInput" type="text"  v-model="searchTxt" placeholder="题库名称" v-on:input ="getexamplenull" @keydown="show($event)"/><label class="el-icon-search TopButtonRightSearch" @click="Ongetexample(searchTxt,0)"></label>
        </div>
      </div>
      <div id="TableDiv" class="W95 Teacher_List_Table">
        <div style="width: 100%;height:calc(100% - 20px);overflow-y: auto;border:1px solid #cdcdcd;"
        v-loading="Gloading"
        element-loading-text="加载中。。。"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)">
          <div style="width:100%;height:40px;line-height:40px;text-align: center">
            <div class="table_header" style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">id</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:calc(30% - 4px);height:100%;border-bottom:1px solid #cdcdcd;">题库名称</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">题目数量</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:20%;height:100%;border-bottom:1px solid #cdcdcd;">最后操作时间</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:20%;height:100%;border-bottom:1px solid #cdcdcd;">操作</div>

            <span v-for="data in tableData">
              <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.id}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:calc(30% - 12px);height:100%;padding:0 4px;border-bottom:1px solid #cdcdcd;">{{data.name}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.numberProblems}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:20%;height:100%;border-bottom:1px solid #cdcdcd;">{{data.updatetime}}</div>
            <div class='ListBorder'></div>
            <div style="float:left;width:20%;height:100%;border-bottom:1px solid #cdcdcd;">
                <el-button type="text" @click="showDialog1(data.id,data.name)">编辑</el-button>
                <el-button type="text" @click="GoTestDetail(data.id,data.name)">管理试题</el-button>
                <el-button type="text" @click="Ondeleteexample(data.id)">删除</el-button>
            </div>
            </span>
          </div>
          <not-content v-if="NotDis"></not-content>
        </div>
      </div>
      <el-pagination
        style="margin-right:20px;float:right;"
        :page-size="Page.pgLimit"
        @current-change="handleCurrentChange"
        layout="prev, pager, next, jumper"
        :total="Page.pgTotalCount">
      </el-pagination>
      <el-dialog :visible.sync="dialogVisible" title="添加题库" size="tiny" >
        <div style="width:100%;height:60px;border:1px solid #cdcdcd">
            <el-form label-width="80px" style="margin-top:10px;">
                <el-form-item label="题库名称">
                    <el-input v-model="name" style="width:90%;" @keyup.enter.native="Onaddexample" id="homename"></el-input>
                </el-form-item>
            </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="Onaddexample" :disabled="name===''">提 交</el-button>
        </span>
      </el-dialog>
      <el-dialog :visible.sync="dialogVisible1" title="修改题库名称" size="tiny" >
        <div style="width:100%;height:60px;border:1px solid #cdcdcd">
            <el-form label-width="80px" style="margin-top:10px;">
                <el-form-item label="题库名称">
                    <el-input v-model="changTestname" style="width:90%;" @keyup.enter.native="OnUpdExample" id="Chomename"></el-input>
                </el-form-item>
            </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="OnUpdExample" :disabled="changTestname===''">提 交</el-button>
        </span>
      </el-dialog>
    </div>
</template>

<script>
import { getOneexample,delexample,addexample,getexample,changeexample } from '../../../api/api';
import NotContent from '../Modules/NotContent';
import { formatDate } from '../../../util/util';
export default {
  data() {
      return {
        NotDis:false,
        tableData: [],
        data: [],
        mobile:'',
        Page:'',
        tof:'',
        searchTxt:'',
        Gloading:true,
        dialogVisible:false,
        dialogVisible1:false,
        changTestid:'',
        changTestname:'',
        name:'',
      }
    },
    components:{
        NotContent
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }else{
          var loginParams = { name: '',pgPageNum:0};
          getexample(loginParams).then(data =>{
            this.Page = data;
            // console.log(data);
            if(data.resList!==undefined){
              this.NotDis = false;
              this.tableData = data.resList;
              for(var i=0;i<this.tableData.length;i++){
                var date = new Date(this.tableData[i].updateTime);
                this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
              }
            }else{
              this.NotDis = true;
            }
            this.Gloading = false;
          })
        }
    },
    methods:{

    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    show(ev){
      if(ev.keyCode===13){
        this.Ongetexample(this.searchTxt,0);
      }
    },
    handleCurrentChange(currentPage){
        this.Ongetexample(this.searchTxt,currentPage);
    },
    getexamplenull(){
      if(this.searchTxt===''){
        this.Ongetexample('',0);
      }
    },
    Ongetexample(txt,num){
      var loginParams = { name: txt,pgPageNum:num};
      getexample(loginParams).then(data =>{
        // console.log(data);
        this.Page = data;
        if(data.resList!==undefined){
          this.NotDis = false;
          this.tableData = data.resList;
          for(var i=0;i<this.tableData.length;i++){
             var date = new Date(this.tableData[i].updateTime);
            this.tableData[i].updatetime = formatDate(date, 'yyyy-MM-dd hh:mm');
          }
        }else{
          this.NotDis = true;
        }
          this.Gloading = false;
      })
    },
    GoTestDetail(id,name){
      window.sessionStorage.setItem('saasTestid',id);
      window.sessionStorage.setItem('saasTestname',name);
      this.$router.push('/Test_Detail');
    },
    addhomeclick(){
        this.dialogVisible = true;
        this.name = '';
        setTimeout(function() {
            document.querySelector('#homename input').focus();
        }, 20);
    },
    Onaddexample(){
      var loginParams = { name: this.name};
      addexample(loginParams).then(data =>{
        // console.log(data);
        if(data.success){
          this.dialogVisible = false;
          this.Ongetexample();
        }else{
          this.openError(data.resMsg);
        }
      })
    },
    showDialog1(id,name){
      this.changTestid = id;
      this.changTestname = name;
      this.dialogVisible1 = true;
      setTimeout(function() {
        document.querySelector('#Chomename input').focus();
      }, 20);
    },
    OnUpdExample(){
      var loginParams = { name: this.changTestname,id:this.changTestid};
      changeexample(loginParams).then(data =>{
        if(data.success){
          this.dialogVisible1 = false;
          for(var i in this.tableData){
            if(this.tableData[i].id===this.changTestid){
              this.tableData[i].name = this.changTestname;
            }
          }
        }else{
          this.openError(data.resMsg);
        }
      })
    },
    Ondeleteexample(id){
      this.$confirm('此操作将永久删除该管理员, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
          }).then(() => {
            delexample(id).then(data =>{
              if(data.success){
                for(var i in this.tableData){
                  if(this.tableData[i].id===id){
                    this.tableData.splice(i,1);
                  }
                }
              }else{
                this.openError(data.resMsg);
              }
            })
          }).catch(() => {
      });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



