<template>

  <form id="form" action="http://192.168.5.4:3000/file" method="post">
    <input type="file" name="files" id="files">
    <input type="button" @click="qwe" value="提交">
  </form>
</template>

<script>
export default {
  data () {
    return {
      msg: '2'
    }
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }
  },
  methods:{
    qwe(){
      var options = {
        // dataType: 'text/html',
        // 文件上传完成后，执行的回调函数
        success: function (e, data) {
          // console.log(e);
          // console.log(data);
          // console.log(data.result.picAddr)
          // 将图片赋给展示出来 fileuploadPic img
          // $('.fileuploadPic img').attr('src',data.result.picAddr);
          // 将图片信息赋给 隐藏的 input 框
          // $('#brandLogo').val(data.result.picAddr)
        },
        error:function(XmlHttpRequest, textStatus, errorThrown){
          // console.log("error:"+XmlHttpRequest);
          // console.log("error:"+errorThrown);
        }
      }
      this.Jq("#form").ajaxSubmit(options);
    }
  }

}
</script>

