
<template>
  <div>
        <br/>
        <br/>
        <br/>
      <el-form ref="form" :model="form" label-width="100px" style="position: relative;">
        <el-form-item label="当前头像">
          <!-- <img src="./../../../../static/img/img.jpg" class="MyData_Img" /> -->

        <img :src="form.myicon" onerror="this.onerror=null;this.src='./../../../static/img/avatar.jpg'" class="MyData_Img"/>
        <div class="MyData_Img cover" id="cover" @mousemove="covermove" @mouseout="coverout" @click="GoUpdIcon">
        </div>
        <div class="coverB" id="coverB" @mousemove="covermove" @mouseout="coverout" @click="GoUpdIcon"></div>
        <div class="coverT" id="coverT" @mousemove="covermove" @mouseout="coverout" @click="GoUpdIcon">编辑头像</div>
        </el-form-item>
        <el-form-item label="昵称">
          <el-input v-model="form.nickname" style="width: 200px;" :maxlength="10"></el-input>
        </el-form-item>
        <el-form-item label="姓名">
          <el-input v-model="form.name" style="width: 200px;" :maxlength="10"></el-input>
        </el-form-item>
        <el-form-item label="生日">
          <el-col :span="11">
            <el-date-picker type="date" placeholder="选择日期" v-model="form.birthday" :clearable='false' style="width: 200px"></el-date-picker>
          </el-col>
        </el-form-item>
        <br/>
        <el-form-item label="性别">
          <el-radio-group v-model="form.gender">
            <el-radio label="男"></el-radio>
            <el-radio label="女"></el-radio>
            <el-radio label="其他"></el-radio>
          </el-radio-group>
        </el-form-item>
      <br/>
      <el-form-item>
        <el-button type="primary" @click.native.prevent="onSubmit" style="width:120px">提交</el-button>
      </el-form-item>
      </el-form>
</div>
</template>

<script>
import { updUserInfo } from '../../../api/api';
import { formatDate } from '../../../util/util';
export default {
  data () {
    return {
      form: {
        myicon: window.localStorage.getItem("saaslogoUrl"),
        name: window.localStorage.getItem("saasname"),
        nickname: window.localStorage.getItem("saasnickname"),
        birthday: window.localStorage.getItem("saasbirthday")!='' ? formatDate(new Date(parseInt(window.localStorage.getItem("saasbirthday"))), 'yyyy-MM-dd hh:mm'):'',
        // birthday: window.localStorage.getItem("saasbirthday"),
        gender:window.localStorage.getItem("saasgender"),
      }
    }
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }
  },
  methods:{
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    onSubmit(){
      // console.log(this.form);
      updUserInfo(this.form).then(data => {
        // console.log(data);
        if(data.success){
          this.openSuccess("修改成功");
        }else{
          this.openError(data.resMsg);
        }
      });
    },
    covermove(){
      var cover = document.getElementById('cover');
      cover.style.opacity = 0.2;
      var coverB = document.getElementById('coverB');
      coverB.style.visibility = 'visible';
      var coverT = document.getElementById('coverT');
      coverT.style.visibility = 'visible';
    },
    coverout(){
      var cover = document.getElementById('cover');
      cover.style.opacity = 0;
      var coverB = document.getElementById('coverB');
      coverB.style.visibility = 'hidden';
      var coverT = document.getElementById('coverT');
      coverT.style.visibility = 'hidden';
    },
    GoUpdIcon(){
      this.$router.push('/UpdIcon');
    }
  },
}
</script>
<style>
.q{
  position: absolute;
}
.cover{
  position: absolute;left:0px;top:0px;z-index:10;background: #dcdcdc;opacity: 0;
}

.coverB{
  position: absolute;left:0px;top:170px;z-index:10;visibility:hidden;
  width:200px;height: 30px;background: black;cursor: pointer;opacity: 0.4;
}
.coverT{
  position: absolute;left:0px;top:170px;z-index:15;visibility:hidden;
  width:200px;height: 30px;line-height: 30px;text-align: center;cursor: pointer;color: #cdcdcd;font-size: 12px
}
</style>



