import Vue from 'vue'
import Router from 'vue-router'


import Login from './../components/Login'//登录
import Basis from './../components/Basis'//顶部菜单
import Main from './../components/Main'//主体
import Download from './../components/Download'//下载页面
import About from './../components/About'//关于页面

import NotFound from './../components/views/404'//404

import Home from './../components/views/Home'//没用到的板块
import Business from './../components/views/Business'//左侧菜单
import Setting from './../components/views/Setting'//设置
import Service from './../components/views/Service'//没用到的板块
import Mall from './../components/views/Mall'//没用到的板块
import Learn from './../components/views/Learn'//没用到的板块
import DataAndPwd from './../components/views/DataAndPwd'//个人信息和密码

import UpdPwd from './../components/views/DataAndPwd/UpdPwd'//修改密码
import MyData from './../components/views/DataAndPwd/MyData'//个人资料

import UpdMsg from './../components/views/MyData/UpdMsg'//修改个人信息
import UpdIcon from './../components/views/MyData/UpdIcon'//修改个人头像

import Organization_List from './../components/views/Setting/Organization_List'//组织机构
import Organization_Settings from './../components/views/Setting/Organization_Settings'//组织设置
import Administrator_List from './../components/views/Setting/Administrator_List'//管理员
import File_List from '../components/views/Setting/File_List'//文件上传
import Department_List from './../components/views/Setting/Department_List'//部门管理
import Personnel_List from './../components/views/Setting/Personnel_List'//人员管理
import Tag_List from './../components/views/Setting/Tag_List'//人员管理
import AddDepartment from './../components/views/Setting/AddDepartment'//添加部门
import AddPersonnel from '../components/views/Setting/AddPersonnel'//添加成员
import AddTag from '../components/views/Setting/AddTag'//添加标签

import Exam_List from './../components/views/business/Exam_List'//考试列表
import AddExam from './../components/views/business/AddExam'//添加考试
import UpdExam from './../components/views/business/UpdExam'//编辑考试
import Paper_List from './../components/views/business/Paper_List'//试卷列表
import AddPaper from './../components/views/business/AddPaper'//添加试卷
import UpdPaper from './../components/views/business/UpdPaper'//编辑试卷
import Test_List from './../components/views/business/Test_List'//题库列表
import Topic_List from './../components/views/business/Topic_List'//题目列表
import Statistic_List from './../components/views/business/Statistic_List'//统计列表
import Exam_Statistic from './../components/views/business/Exam_Statistic'//考试统计
import Exam_Person from './../components/views/business/Exam_Person'//考试统计
import Teacher_List from './../components/views/business/Teacher_List'//教师列表
import AddTeacher from './../components/views/business/AddTeacher'//添加教师
import Resource_List from './../components/views/business/Resource_List'//课程管理
import AddResource from '../components/views/business/AddResource'//添加课程
import UpdResource from '../components/views/business/UpdResource'//修改课程
import FirstP_List from './../components/views/business/FirstP_List'//首页列表
import AddCourseSort from './../components/views/business/AddCourseSort'//添加分类
import AddCourseSortT from './../components/views/business/AddCourseSortT'//添加同级分类
import UpdCourseSort from './../components/views/business/UpdCourseSort'//修改分类

import Chapter from './../components/views/business/Chapter'//章节
import Exam_Result from './../components/views/business/Exam_Result'//章节--查看考试结果

import Plate from './../components/views/business/FirstP_List/Plate'//板块
import Carousel from './../components/views/business/FirstP_List/Carousel'//轮播图列表
import AddCarousel from './../components/views/business/FirstP_List/AddCarousel'//增加轮播图
import UpdCarousel from './../components/views/business/FirstP_List/UpdCarousel'//修改轮播图
import Advert from './../components/views/business/FirstP_List/Advert'//广告图列表
import AddAdvert from './../components/views/business/FirstP_List/AddAdvert'//增加广告图
import UpdAdvert from './../components/views/business/FirstP_List/UpdAdvert'//修改广告图


import Course from './../components/views/business/Resource_List/Course'//课程
import CourseSort from './../components/views/business/Resource_List/CourseSort'//分类

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/Login',
      component: Login
    },
    {
      path: '/',
      component: Basis,
      children: [
        {
          path: '/Home',
          component: Home
        },
        {
          path: '/Business',
          component: Business,
          children: [
            {
              path: '/FirstP_List',
              component: FirstP_List,
              children:[
                {
                  path: '/Plate',
                  component: Plate
                },
                {
                  path: '/Carousel',
                  component: Carousel
                },
                {
                  path: '/AddCarousel',
                  component: AddCarousel
                },
                {
                  path: '/UpdCarousel',
                  component: UpdCarousel
                },
                {
                  path: '/Advert',
                  component: Advert
                },
                {
                  path: '/AddAdvert',
                  component: AddAdvert
                },
                {
                  path: '/UpdAdvert',
                  component: UpdAdvert
                }
              ]
            },
            {
              path: '/Topic_List',
              component: Topic_List,
            },
            {
              path: '/Exam_List',
              component: Exam_List,
            },
            {
              path: '/AddExam',
              component: AddExam,
            },
            {
              path: '/UpdExam',
              component: UpdExam,
            },
            {
              path: '/Paper_List',
              component: Paper_List,
            },
            {
              path: '/AddPaper',
              component: AddPaper,
            },
            {
              path: '/UpdPaper',
              component: UpdPaper,
            },
            {
              path: '/Test_Detail',
              component: Topic_List,
            },
            {
              path: '/Test_List',
              component: Test_List,
            },
            {
              path: '/Statistic_List',
              component: Statistic_List,
            },
            {
              path: '/Exam_Statistic',
              component: Exam_Statistic,
            },
            {
              path: '/Exam_Person',
              component: Exam_Person,
            },
            {
              path: '/Teacher_List',
              component: Teacher_List,
            },
            {
              path: '/AddTeacher',
              component: AddTeacher,
            },
            {
              path: '/Chapter',
              component: Chapter
            },
            {
              path: '/Exam_Result',
              component: Exam_Result
            },
            {
              path: '/AddCourseSort',
              component: AddCourseSort
            },
            {
              path: '/AddCourseSortT',
              component: AddCourseSortT
            },
            {
              path: '/UpdCourseSort',
              component: UpdCourseSort
            },
            {
              path: '/Resource_List',
              component: Resource_List,
              children: [
                {
                  path: '/Course',
                  component: Course
                },
                {
                  path: '/CourseSort',
                  component: CourseSort
                },
                {
                  path: '/AddResource',
                  component: AddResource,
                },
                {
                  path: '/UpdResource',
                  component: UpdResource,
                }
              ]
            }
          ]
        },
        {
          path: '/Setting',
          component: Setting,
          children: [
            {
              path: '/Organization_List',
              component: Organization_List
            },
            {
              path: '/Organization_Settings',
              component: Organization_Settings
            },
            {
              path: '/Administrator_List',
              component: Administrator_List
            },
            {
              path: '/File_List',
              component: File_List
            },
            {
              path: '/AddDepartment',
              component: AddDepartment
            },
            {
              path: '/Department_List',
              component: Department_List
            },
            {
              path: '/Personnel_List',
              component: Personnel_List
            },
            {
              path: '/AddPersonnel',
              component: AddPersonnel
            },
            {
              path: '/Tag_List',
              component: Tag_List
            },
            {
              path: '/AddTag',
              component: AddTag
            },
          ]
        },
        {
          path: '/Service',
          component: Service
        },
        {
          path: '/Mall',
          component: Mall
        },
        {
          path: '/Learn',
          component: Learn
        },
        {
          path: '/DataAndPwd',
          component: DataAndPwd,
          children:[
            {
              path: '/MyData',
              component: MyData,
              children: [
                {
                  path: '/UpdMsg',
                  component: UpdMsg,
                },
                {
                  path: '/UpdIcon',
                  component: UpdIcon,
                }
              ]
            },
            {
              path: '/UpdPwd',
              component: UpdPwd
            },
          ]
        }
      ]
    },
    {
      path: '*',
      component: NotFound
    },
    {
      path: '/download',
      component: Download,
    },
    {
      path: '/about',
      component: About,
    },
  ]
})



