          <div style="margin-top:10px"><el-radio v-model="radio" label="2"><span style="padding-left:10px">文档(docx、ppt、pdf、txt、html等)</span></el-radio></div>
          <div style="margin-top:10px"><el-radio v-model="radio" label="1"><span style="padding-left:10px">图片文件(jpg、png、jepg、bmp、gif等)</span></el-radio></div>
          <div style="margin-top:10px"><el-radio v-model="radio" label="6"><span style="padding-left:10px">其他文件(rar、zip等)</span></el-radio></div>
