
<template>
  <div style="width:100%;height:100%;">
      <div style="background: #F5F5F5">
        <div class="TopName W95"><i class="el-icon-close" style="margin-right:10px;cursor: pointer;" @click="Back"></i>返回</div>
      </div>
      <div class="TopLine"></div>
        <br/>
        <br/>
      <el-form ref="form" :model="form" label-width="100px" id="form" method="post">
        <el-form-item label="广告名称">
          <el-input v-model="form.name" style="width: 385px;" id="livename" autofocus ></el-input>
        </el-form-item>
        <el-form-item label="广告封面">
          <template>
            <div class="ADDUploadImage_Div">
                <!-- <img src="./../../../../static/img/img.jpg" /> -->
                <canvas id="tp" v-if="icontof" style="width:200px;height:400px;"></canvas>
                <img v-else id="myicons" style="width:200px;height:400px;" :src="myicons" onerror="this.onerror=null;this.src='./../../../static/img/avatar.jpg'"/>
                <div>
                    <div style="width:230px;height:100px;margin-top:10px;">*仅支持JPG、PNG格式的图片文件 文件大小小于5M</div>
                    <div style="width:230px;height:30px;position: relative;">
                        <el-button size="small" style="width:80px">本地上传</el-button>
                        <input id="myfile" type="file" name="files" multipart class="UIfile" title="点击选择本地文件" @change="getFile($event)" accept="image/jpeg,image/png"/>
                        <div style="display:none">{{qwe}}</div>
                    </div>
                </div>
            </div>
          </template>
        </el-form-item>
        <br/>
        <el-form-item label="">
            <el-button type="primary" size="large" style="width:100px" @click="Onaddadvert">提 交</el-button>
        </el-form-item>
      </el-form>
    </div>
</template>

<script>
  import { addadvert,uploadImg } from '../../../../api/api';
  export default {
    data () {
      return {
        myicons:'',
        page:'',
        searchTxt:'',
        files: '',
        qwe:'',
        imageUrl: '',
        icontof:true,
        ie9tof:navigator.userAgent.indexOf("MSIE 9.0")>0,
        form: { name: '' },
        JUrl:window.localStorage.getItem('saasJUrl')
      }
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }
    },
    methods:{
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      Onaddadvert(){
        console.log(this.files);
        // this.successRes('1');
        if(this.files!==""){
          var options = {
            url:this.JUrl,
            type:'post',
            enctype:'multipart/form-data',
            error:function(){
              this.openError('上传失败');
            },
            success:this.successRes
          }
          this.Jq("#form").ajaxSubmit(options);
        }else{
          this.openError('请选择图片');
        }
      },
      successRes(jsondata){
        var data = JSON.parse(jsondata);
        if(data.resMsg === "success"){
          window.localStorage.setItem("saaslogoUrl",data.url);
            console.log(data.url);
            var loginParams = { name: this.form.name, cover: 'http://120.132.18.193:8080/img/M00/00/02/ChebIFpljBiAQbbnAABi8tfV3EI882.png' };
            var loginParams = { name: this.form.name,cover: data.url };
            // console.log(loginParams);
            addadvert(loginParams).then(data =>{
              console.log(data);
              if(data.success){
                this.$router.push('/Advert');
              }else{
                this.openError(data.reqMsg);
              }
            });
          }else{
            this.openError(data.msg);
          }
      },
      Back:function(){
          this.$router.push('/Advert');
      },
      getFile(objs) {
        this.files = '123';
        if(this.ie9tof){
          this.icontof = true;
          var obj = document.getElementById("myfile");
          obj.select(); obj.blur();
          this.files = document.selection.createRange().text;
          document.selection.empty();
          document.getElementById("tp").style.filter="progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale',src='"+this.files+"')";
        }else{
          this.icontof = false;
          this.qwe++;
          setTimeout(function() {
          var myicons = document.getElementById("myicons");
          this.files = objs.target.files[0];
          // console.log(this.files);
          var reader = new FileReader();
          reader.readAsDataURL(this.files);
          reader.onload = function (e) {
              myicons.src = this.result;
          }
          }, 100);
        }
      }
    }
  }
</script>



