<template>
  <div style="width:100%;height:100%;">
    <div>
      <div class="TopName W95"><i class="el-icon-close" style="margin-right:10px;cursor: pointer;" @click="goBack"></i>{{title}}</div>
      <!-- <iframe name="frame1" id="frame1" frameborder="0" height="40" width="800"></iframe> -->
    </div>
    <div class="TopLine"></div>
    <div class="TopContent" style="height: 100%;">
      <el-form :model="form" label-width="100px" style="margin-top:15px;overflow-y:auto" id="form" method="post" >
        <el-form-item label="姓名">
          <el-input v-model="form.name" style="width: 385px;" placeholder="请输入姓名" @change="" id="livename" autofocus ></el-input>
        </el-form-item>
        <el-form-item label="手机号">
          <el-input v-model="form.mobile" style="width: 385px;" placeholder="请输入手机号" @change="" id="livename" autofocus ></el-input>
        </el-form-item>
        <el-form-item label="登录密码">
          <el-input v-if="title==='添加成员'" v-model="form.password" type="password" style="width: 385px;" placeholder="******" @change="" id="livename" autofocus ></el-input>
          <el-input v-if="title==='编辑成员'" v-model="form.password" type="password" style="width: 385px;" placeholder="******" @change="" id="livename" autofocus ></el-input>
          <span>* 默认密码为手机号后六位</span>
        </el-form-item>
        <el-form-item label="所属部门">
          <el-input
            type="textarea"
            :rows="4"
            readonly style="width:385px;"
            placeholder="请选择部门"
            v-model="form.parentName">
          </el-input>
          <el-button type="default" @click="ChooseDepartment">选择部门</el-button>
        </el-form-item>

        <el-form-item label="" >
          <el-button type="primary"style="width:120px" @click="OnaddUser" >提交</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--选择部门-->
    <el-dialog :visible.sync="dialogVisible" :title="titlename" top="5%" :before-close="closeDialog">
      <div class="Search">
        <div class="SearchLeft">
          <input v-model="searchTxt" type="text" placeholder="部门名称" style="text-indent: 0.75em;" @keyup.enter="searchDept">
          <el-button type="default" style="padding: 5px 10px;" @click="searchDept">搜索</el-button>
        </div>
      </div>
      <div style="clear: both"></div>
      <br>

      <div style="width:100%;height:auto;border:1px solid #cdcdcd">
        <div class="AddDepartment" id="DeptTopLength_2" v-show="hackReset">
          <el-tree
            ref="tree"
            :data="DepartmentList"
            node-key="id"
            :default-checked-keys="checkedDeptList"
            empty-text="暂无数据"
            show-checkbox accordion check-strictly auto-expand-parent highlight-current
            :indent="20"
            :props="defaultProps" :default-expanded-keys="keys"
          >
          </el-tree>
        </div>
      </div>
      <div style="height:1px; margin: 10px auto;">
        <el-button style="float:right" type="primary" size="small" @click="closeDialog">取消</el-button>
        <el-button style="float:right;margin-right: 10px;" type="primary" size="small" @click="confirmDepartment">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import { UploadFile,getDepartmentAll,getDepartmentById,addUser,updateUser} from '../../../api/api';
  import NotContent from '../Modules/NotContent';

  export default {
    data() {
      return {
        searchTxt:'',
        title:window.localStorage.getItem('saasmember'),
        parent:'',
        form: {
          uid:'',
          name: '',
          mobile: '',
          password: '',
          parent:'',
          parentName:'',
          isActive: true
        },
        titlename:'选择部门',
        activeNames: ['200'],
        deptid: window.localStorage.getItem('saasDeptid'),
        checkedIndex:0,
        checkedDeptList:[],
        submitDeptList:[],
        tempList:[],
        submitUser:[],
        openList: [],
        dialogVisible:false,
        hackReset: false,
        defaultProps: {
          children: 'resList',
          label: 'name'
        },
        Member: JSON.parse(window.localStorage.getItem('saasdataMember')),
        keys: [],
        tofIE9:navigator.appName == "Microsoft Internet Explorer"&&parseInt(navigator.appVersion.split(";")[1].replace(/[ ]/g, "").replace("MSIE",""))<=9
      }
    },
    components:{ NotContent },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }
    },
    mounted(){
      this.hack();
      if(this.title==='添加成员'){
        getDepartmentById(window.localStorage.getItem('saasDeptid')).then(data=>{
          this.form.parentName = data.name;
        });
      }
      if(this.title==='编辑成员'){
        this.form.parent = window.localStorage.getItem('saasmemberDept');
        this.form.uid = this.Member.id;
        this.form.name = this.Member.name;
        this.form.mobile = this.Member.mobile;
        if(this.Member.deptList){
          this.Member.deptList.forEach(item=>{
            if(item!=this.Member.deptList[this.Member.deptList.length-1]){
              this.form.parentName += item.name + ' ';
            }
            else{
              this.form.parentName += item.name;
            }
          });
        }
        else{
          getDepartmentById(window.localStorage.getItem('saasDeptid')).then(data=>{
            this.form.parentName = data.name;
          });
        }
      }
      if(this.deptid!=''){
        this.submitDeptList.push({id:this.deptid});
      }
    },
    computed:{
      DepartmentList(){
        if(this.$store.state.DepartmentList!=''){
          this.NotDis = false;
          return this.$store.state.DepartmentList;
        } else {
          getDepartmentAll().then(res=>{
            if (res.resCode == 0) {
              this.$store.commit('updateDeptList', res.resList)
            }
          })
        }
      },
    },
    methods: {
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      goBack() {
        if(window.localStorage.getItem('saasPrePage')==='Personnel_List'){
          this.$router.push('/Personnel_List');
        }
        else this.$router.push('/Department_List');

        getDepartmentAll(window.localStorage.getItem("saasoid")).then(data=>{
          this.$store.commit("updateDeptList", data.resList);
        });   // 更新部门列表Vuex
      },
      hack(){
        this.hackReset = false; // hack方法强制刷新组件
        this.$nextTick(() => {
          this.hackReset = true;
        });
      },
      ChooseDepartment(){
        this.dialogVisible=true;

        // new add
        if(this.title==='添加成员'){
          this.DepartmentList.forEach(item => {
            item.isCheck = false;
            if(item.resList){
              item.resList.forEach(subItem=>{
                item.isCheck = false;
                if(subItem.resList){
                  item.isCheck = false;
                }
              });
            }
            this.$nextTick(()=>{
              if(this.deptid!='') this.$refs.tree.setChecked(this.deptid, true);
            });
          });
        }

        if(this.title==='编辑成员') {
          this.$nextTick(function () {
            if(this.Member.deptList){
              this.Member.deptList.forEach(item=>{
                this.$refs.tree.setChecked(item.id, true, false);
              });
            }
          });
        }
        this.hack();
      },
      confirmDepartment(){
        this.form.parentName = '', this.submitDeptList = [];
        this.checkedDeptList = this.$refs.tree.getCheckedKeys();
        this.$refs.tree.getCheckedNodes().forEach(item=>{
          if(this.form.parentName!='') this.form.parentName += ',' + item.name;
          else this.form.parentName += item.name;
        });
        this.checkedDeptList.forEach(item=>{
          this.submitDeptList.push({id:item});
        });
        this.dialogVisible=false;
      },
      closeDialog(){
        this.dialogVisible=false;
      },
      OnaddUser(){
        if(!this.form.name){ return this.openError('姓名不能为空！');}
        // if(/\s/.test(this.form.name)){ return this.openError('姓名不能包含空格！');}
        if(this.form.name.trim().length === 0){ return this.openError('姓名不能全是空格！');}
        if(!this.form.mobile){ return this.openError('手机号不能为空！');}
        if(!(/^1[3|4|5|6|7|8][0-9]\d{4,8}$/.test(this.form.mobile))){
          return this.openError('请输入正确的手机号！');
        }
        if(!this.form.parentName){ return this.openError('部门不能为空！');}
        if(this.submitDeptList==''){ return this.openError('提交的部门列表为空，请联系管理员！');}

        if(this.title==='添加成员'&&this.form.name.trim().length!=0&&this.form.mobile&&this.form.name&&this.form.parentName){
          var jse = new this.$jsEncrypt.JSEncrypt();
          addUser(jse, this.form, this.submitDeptList).then(data=>{
            if(data.success){
              this.openSuccess('修改成功');
              if(window.localStorage.getItem('saasPrePage')==='Personnel_List'){
                this.$router.push('/Personnel_List');
              }
              else this.$router.push('/Department_List');
            }else{
              this.openError(data.resMsg);
            }
          });
        }

        if(this.title==='编辑成员'&&!(/\s/.test(this.form.name))&&this.form.name.trim().length!=0&&this.form.mobile&&this.form.name&&this.form.parentName){
          var jse = new this.$jsEncrypt.JSEncrypt();
          addUser(jse,this.form,this.submitDeptList).then(data=>{
            if(data.success){
              this.openSuccess('编辑成功');
              if(window.localStorage.getItem('saasPrePage')=='Department_List'){
                this.$router.push('/Department_List');
              }
              else this.$router.push('/Personnel_List');
            }else{
              this.openError(data.resMsg);
            }
          });
        }
      },
      setCheck(id, flag1, flag2){
        this.$refs.tree.setChecked(id, flag1, flag2);
      },
      recursiveDept(data){
        if(data.resList!=''){
          for(var i=0;i<data.resList.length;i++){
            var item = data.resList[i];
            var result = item.name.match(this.searchTxt);
            if(result != null){
              this.deptid = parseInt(item.id);
              if(this.$refs.tree.getCheckedKeys().indexOf(parseInt(item.id))<0){
                this.setCheck(item.id, true, false);
                this.keys.push(item.id);
                return;
              }
            }
            else{
              this.recursiveDept(item);
            }
          };
        }
        else{
          return;
        }
      },
      searchDept(){
        for(var i=0;i<this.DepartmentList.length;i++){
          var item = this.DepartmentList[i];
          var result = item.name.match(this.searchTxt);
          if(result != null){
            this.deptid = parseInt(item.id);
            if(this.$refs.tree.getCheckedKeys().indexOf(parseInt(item.id))<0){
              this.setCheck(item.id, true, false);
              this.keys.push(item.id);
              return;
            }
          }
          else {
            this.recursiveDept(item);
          }
        };
      }
    }
  }
</script>



