<template>
    <div style="width:100%;height:100%;">
        <div class="TopButton W95">
            <span class="TopButtonLeft">
            <el-button type="primary" @click="addresource">添加课程</el-button>
            </span>
            <div class="TopButtonRight">
              <input class="TopButtonRightInput" type="text" v-model="searchTxt" placeholder="请输入关键字" v-on:input ="getcourses" @keydown="show($event)"/><label class="el-icon-search TopButtonRightSearch" @click="getsearchTxt"></label>
            </div>
        </div>
        <div id="TableDiv" class="W95 Teacher_List_Table" style="overflow:auto;"
        v-loading="Gloading"
        element-loading-text="加载中。。。"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)">
            <div class="Resource_List_div" v-for="data in ResourceData" v-if="hackReset">
                <div style="width:100%;height:68px;">
                    <img onerror="this.src='./../../../../../static/img/avatar.jpg'" :src="data.cover" style="width:120px;height:68px;float:left;margin-right:20px;cursor: pointer;object-fit:cover;" @click="updresource(data)"/>
                    <!-- <img src="./../../../../static/img/avatar.jpg"/> -->
                    <div style="width:150px;height:44px;margin-top:5px;line-height:20px;float:left;" class="OverTwo" >{{data.name}}</div>
                    <div style="width:150px;height:20px;float:left;font-size:12px;overflow: hidden;" class="Over" >主讲人：{{data.lecturerName}}</div>
                </div>
                <div style="width:100%;height:30px;">
                    <div style="height:30px;float:left;line-height:30px;"><el-button type="text" size="mini" @click="addChapter(data.id)">添加章节</el-button></div>
                    <div style="height:30px;float:left;line-height:30px;margin-left:20px"><el-button type="text" size="mini" @click="Ondelcourse(data.id)">删除课程</el-button></div>
                    <div style="height:30px;float:left;line-height:30px;margin-left:20px" v-if="data.liveFlag===1"><el-button type="text" size="mini" @click="liveset(data)">直播参数设置</el-button></div>
                    <div style="height:30px;float:left;line-height:30px;float:right">上架 <el-switch v-model="data.tofDis" on-text="" off-text=""  @change="changedis(data)"></el-switch></div>
                </div>
                <div style="width:100%;height:30px;">
                    <div style="width:65%;height:30px;float:left;">发布时间：{{data.updatetime}}</div>
                    <div style="height:30px;float:left;overflow: hidden;float:right;max-width:100px;" class="Over" v-if="data.creatorName!==undefined">发布人：{{data.creatorName}}</div>
                </div>
                <div style="display:none">{{qwe}}</div>
            </div>
            <not-content v-if="NotDis"></not-content>
        </div>

        <el-pagination
            style="margin-right:20px;float:right;margin-top:10px"
            :page-size="Page.pgLimit"
            @current-change="handleCurrentChange"
            layout="prev, pager, next, jumper"
            :total="Page.pgTotalCount">
        </el-pagination>

        <el-dialog :visible.sync="dialogVisible" title="直播参数设置" style="width:100%;height:1000px;min-width:800px" @close="getcourselist">
          <div style="width:100%;height:220px;border:1px solid #cdcdcd;">
              <el-form label-width="78px" style="margin-top:10px;">
                  <el-form-item label="rtmp地址">
                    <el-input v-model="form.url" :disabled="btnlive" @change="geturl" style="width:calc(100% - 8px);"></el-input>
                  </el-form-item>
              </el-form>
              <div style="width:98%;padding-left:10px">
                开始时间<el-date-picker type="datetime" placeholder="选择日期" :disabled="btnlive" @change="choesetime1" :picker-options="pickerOptions1" v-model="form.begintime" style="width: calc(50% - 72px);margin-left:10px"></el-date-picker>
                结束时间<el-date-picker type="datetime" placeholder="选择日期" :disabled="btnliveend" @change="choesetime2" :picker-options="pickerOptions2" v-model="form.endtime" style="width: calc(50% - 72px);margin-left:10px"></el-date-picker>

              </div>
              <div style="margin-top:20px;">
                <div style="width:auto;display:inline-block;left:50%;position:relative;margin-left:-25%;">
                  <el-button :disabled="btntestlive" @click="testlive" >{{testlivename}}</el-button>
                  <el-button :disabled="form.liveStatus===2||startlivetof" @click="startlive">{{form.liveStatusname}}</el-button>
                  <el-button :disabled="pickertof" type="primary" @click="changelive">提交</el-button>
                </div>
              </div>
          </div>
        </el-dialog>
    </div>
</template>

<script>
  import { getcourse,updcourse,delcourse,getorganization,getchapter } from '../../../../api/api';
  import NotContent from '../../Modules/NotContent';
  import { formatDate } from '../../../../util/util';
  import axios from 'axios';
  export default {
    data() {
      return {
        Gloading:true,
        value1: true,
        qwe:0,
        searchTxt:'',
        ResourceData:[],
        Page:'',
        NotDis:false,
        dialogVisible: false,
        form:{
          id:'',
          name:'',
          url:'',
          Turl:'',
          begintime:'',
          endtime:'',
          liveStatus:'',
          liveStatus:'',
          liveStatusname:'',
          radio: ''
        },
        testlivename: '',
        btntestlive: false,   // 是否调试状态
        btnlive: false,       // 是否直播状态
        btnliveend: false,       // 是否直播结束状态
        hackReset: false,
        pickerOptions1:{
          disabledDate(time){
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        pickerOptions2:{
          disabledDate(time){
            return time.getTime() < Date.now() - 8.64e7;
          }
        },
        pickertof:false,
        startlivetof:false
      }
    },
    beforeUpdate:function(){
      var THeight = document.getElementById("TableDiv");
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }else{

        }
    },
    mounted(){this.getcourselist();},
    components:{ NotContent },
    methods:{
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      hack(){
        this.hackReset = false; // hack方法强制刷新组件
        this.$nextTick(() => {
          this.hackReset = true;
        })
      },
      getlive(id){
        // console.log(id,this.Url2);
        let param = {courseId: id};
        axios.get(this.Url2, {
          params: param
        }).then(res=>{
          // console.log(res.data);
          var str = res.data.stdout.split('\n'), arr = [];
          for(var i=1;i<str.length-1;i++){
            arr.push({url:'http://120.132.18.193:8080/vrd/live/'+str[i]});
          }
        });
      },
      getcourselist(){
        var loginParams = { name: this.searchTxt,pgPageNum:1};
        getcourse(loginParams).then(data =>{
          // console.log(data);
          var that = this;
          that.Gloading = false;
          if(data.success){
            if(data.resList!==undefined){
              that.NotDis = false;
              that.ResourceData = data.resList;
              that.Page = data;
              var nowtime = new Date().getTime();
              for(var i=0;i<that.ResourceData.length;i++){
                var d = new Date(that.ResourceData[i].updateTime);
                that.ResourceData[i].updatetime = formatDate(d, 'yyyy-MM-dd hh:mm');
                if(nowtime<that.ResourceData[i].beginTime){
                  that.ResourceData[i].liveStatus = 0;
                  that.ResourceData[i].liveStatusname = '立即开始直播';
                }else if(nowtime>that.ResourceData[i].endTime){
                  that.ResourceData[i].liveStatus = 2;
                  that.ResourceData[i].liveStatusname = '直播已结束';
                }else if(that.ResourceData[i].beginTime===undefined||that.ResourceData[i].endTime===undefined){
                  // that.ResourceData[i].liveStatus = 0;
                  if(that.ResourceData[i].liveStatus!==4) that.ResourceData[i].liveStatus = 0;
                  that.ResourceData[i].liveStatusname = '立即开始直播';
                }else{
                  that.ResourceData[i].liveStatus = 1;
                  that.ResourceData[i].liveStatusname = '立刻结束直播';
                }

                if(that.ResourceData[i].status===1){
                  that.ResourceData[i].tofDis = false;
                }else{
                  that.ResourceData[i].tofDis = true;
                }
              }
            }else{
              that.NotDis = true;
            }
          }else{
            that.openError(data.resMsg);
          }
          this.hack();
        });
      },
      changelive(){
        var begintime = '';
        var endtime = '';
        for(var i=0;i<this.ResourceData.length;i++){
          if(this.ResourceData[i].id===this.form.id){
            if(this.ResourceData[i].beginTime !== this.form.begintime){
              begintime = Date.parse(this.form.begintime);
            }else{
              begintime = this.form.begintime;
            }
            if(this.ResourceData[i].endTime !== this.form.endtime){
              endtime = Date.parse(this.form.endtime);
            }else{
              endtime = this.form.endtime;
            }
          }
        }
        // console.log(begintime);
        // console.log(endtime);
        var nowtime = new Date().getTime();
        var startlive = '';
        if(begintime!==''&&endtime!==''){
          if(nowtime<begintime){
            startlive = 0;
          }else if(nowtime>endtime){
            startlive = 2;
          }else if(begintime===undefined||endtime===undefined){
            startlive = 0;
          }else{
            startlive = 1;
          }
        }
        if(this.form.begintime!==''&&this.form.endtime!==''){
          var loginParam = {id:this.form.id,live:this.form.url,begintime:begintime,endtime:endtime,visit:this.form.radio,liveStatus:startlive};
              updcourse(loginParam).then(data =>{
                if(data.success){
                  this.openSuccess('修改成功');
                  this.dialogVisible = false;
                  var url = this.form.url;
                  this.form.url = '';
                  for(var j=0;j<this.ResourceData.length;j++){
                    if(this.ResourceData[j].id===this.form.id){
                      if(begintime!==''){
                        this.ResourceData[j].beginTime = begintime;
                      }
                      if(endtime!==''){
                        this.ResourceData[j].endTime = endtime;
                      }
                      // console.log(this.ResourceData[j].beginTime+'==='+this.ResourceData[j].endTime);
                      var nowtime = new Date().getTime();
                      if(nowtime<this.ResourceData[j].beginTime){
                        this.ResourceData[j].liveStatus = 0;
                        this.ResourceData[j].liveStatusname = '立即开始直播';
                      }else if(nowtime>this.ResourceData[j].endTime){
                        this.ResourceData[j].liveStatus = 2;
                        this.ResourceData[j].liveStatusname = '直播已结束';
                      }else if(this.ResourceData[j].beginTime===undefined||this.ResourceData[j].endTime===undefined){
                        this.ResourceData[j].liveStatus = 0;
                        this.ResourceData[j].liveStatusname = '立即开始直播';
                      }else{
                        this.ResourceData[j].liveStatus = 1;
                        this.ResourceData[j].liveStatusname = '立刻结束直播';
                      }
                      this.ResourceData[j].live = url;
                      this.ResourceData[j].visit = this.form.radio;
                    }
                  }
                  this.qwe++;
                }else{
                  this.openError(data.resMsg);
                }
              })
        }else{
          this.openError('请选择时间');
        }
      },
      geturl(){
        if(this.form.url!==''){
          // if(this.form.begintime!==''&&this.form.endtime!==''){
            this.pickertof = false;
          // }else{
          //   this.pickertof = true;
          // }
        }else{
            this.pickertof = true;
        }
        // this.form.Turl = this.form.url+'/'+this.form.id;
      },
      choesetime1(){
        this.pickertof = true;
        if(this.form.endtime!==''){
          if(Date.parse(this.form.begintime)>Date.parse(this.form.endtime)||Date.parse(this.form.begintime)>this.form.endtime){
            this.openError('直播开始时间不能晚于结束时间');
            this.form.begintime = '';
          }else{
            if(this.form.begintime!==''&&this.form.begintime!==undefined){
              this.pickertof = false;
              this.startlivetof = false;
            }
          }
        }
      },
      choesetime2(){
        this.pickertof = true;
        if(this.form.begintime!==''){
          if(Date.parse(this.form.begintime)>Date.parse(this.form.endtime)||this.form.begintime>Date.parse(this.form.endtime)){
            this.openError('直播结束时间不能早于开始时间');
            this.form.endtime = '';
          }else{
            if(this.form.endtime!==''&&this.form.endtime!==undefined){
              this.pickertof = false;
              this.startlivetof = false;
            }
          }
        }
      },
      testlive(){
        var status;
        if(this.testlivename === '结束调试') status = 0;
        else status = 4;
        var loginParam = {id:this.form.id, liveStatus:status, live:this.form.url};
        // console.log(loginParam);
        updcourse(loginParam).then(data => {
          if(data.success){
            if(data.liveStatus===4) this.testlivename = '结束调试';
            if(data.liveStatus===0) this.testlivename = '调试开始';
            this.hack();
          }
          else this.openError(data.resMsg);
        });
      },
      startlive(){
        if(this.form.begintime==='') return this.openError('直播开始时间不能为空！');
        if(this.form.endtime==='') return this.openError('直播结束时间不能为空！');
        var begintime = '', endtime = '';
        for(var i=0;i<this.ResourceData.length;i++){
          if(this.ResourceData[i].id===this.form.id){
            if(this.ResourceData[i].beginTime !== this.form.begintime){
              begintime = this.form.begintime;
              // console.log('b:'+begintime);
            }
            if(this.ResourceData[i].endTime !== this.form.endtime){
              endtime = this.form.endtime;
              // console.log('e:'+endtime);
            }
          }
        }

        var nowtime = new Date().getTime();
        var myDate = new Date();
        // console.log('nowtime: '+nowtime);
        // console.log('begintime: '+this.form.begintime);
        // console.log('endtime: '+this.form.endtime);
        var liveStatus;
        if(this.form.liveStatus===0){   // 直播未开始状态
          if(this.form.begintime>this.form.endtime){
            this.openError('直播结束时间不能早于开始时间');
          }else{
            this.form.begintime = nowtime;
            if(!Number.isInteger(this.form.endtime)) this.form.endtime = this.form.endtime.getTime();
            begintime = myDate;
            liveStatus = 1;
          }
        }
        else if(this.form.liveStatus===1){   // 直播已开始状态
          if(this.form.begintime>this.form.endtime){
            this.openError('直播结束时间不能早于开始时间');
          }else{
            this.form.endtime = nowtime;
            if(!Number.isInteger(this.form.begintime)) this.form.begintime = this.form.begintime.getTime();
            endtime = myDate;
            liveStatus = 2;
          }
        }

        // console.log('=======================');
        // console.log('this.form.liveStatus: '+this.form.liveStatus);
        // console.log('this.form.begintime: '+this.form.begintime);
        // console.log('this.form.endtime: '+this.form.endtime);
        if(begintime!==''||this.form.begintime!==''){
          if(endtime!==''||this.form.endtime!==''){
            // change
            // var loginParam = {id:this.form.id,liveStatus:this.form.liveStatus,begintime:this.form.begintime,endtime:this.form.endtime,name:this.form.name, live:this.form.url};
            var begin, end;
            if(!Number.isInteger(this.form.begintime)) begin = this.form.begintime.getTime();
            else begin = this.form.begintime;
            if(!Number.isInteger(this.form.endtime)) end = this.form.endtime.getTime();
            else end = this.form.endtime;
            var loginParam = {id:this.form.id, liveStatus:liveStatus, begintime:begin, endtime:end, live:this.form.url};
            // console.log(loginParam);
            updcourse(loginParam).then(data => {
              if(data.success){
                if(liveStatus===1){
                  this.openSuccess('直播已开始');
                  this.btntestlive = true;
                  this.btnlive = true;
                  this.form.liveStatus= 1;
                  this.form.liveStatusname = '立刻结束直播';
                }
                else if(liveStatus===2){
                  this.openSuccess('直播已结束');
                  this.form.liveStatus= 2;
                  this.pickertof = true;    // 禁用提交按钮
                  this.form.liveStatusname = '直播已结束';
                }

                this.btntestlive = true;
                this.testlivename = '结束调试';

                for(var j=0;j<this.ResourceData.length;j++){
                  if(this.ResourceData[j].id===this.form.id){
                    if(begintime!==''){
                      this.ResourceData[j].beginTime = begintime;
                      this.form.begintime = begintime;
                    }
                    if(endtime!==''){
                      this.ResourceData[j].endTime = endtime;
                      this.form.endtime = endtime;
                    }
                    this.ResourceData[j].liveStatus = this.form.liveStatus;
                    // console.log('this.ResourceData[j].liveStatus: '+this.ResourceData[j].liveStatus);
                  }
                }
                this.qwe++;
              }else{
                this.openError(data.resMsg);
              }
            });
          }else{
            this.openError('请选择直播结束时间');
          }
        }else{
          this.openError('请选择直播开始时间');
        }
      },
      liveset(data){
        if(data.beginTime===undefined&&data.endTime===undefined&&data.visit===0&&data.live===undefined){
          this.startlivetof = true;
          this.pickertof = true;
        }
        this.form.id = data.id;
        this.form.name = data.name;
        this.form.begintime = data.beginTime == undefined ? '' : data.beginTime;
        this.form.endtime = data.endTime == undefined ? '' : data.endTime;
        this.form.radio = data.visit == undefined ? '0' : data.visit+'';
        this.form.url = data.live == undefined ? '/'+data.id : data.live;
        // 获取直播地址
        getorganization().then(resdata =>{
          if(resdata.success){
            if(resdata.livepath) this.form.url = data.live==undefined ? resdata.livepath+'/'+data.id : data.live;
          }
        });

        if(data.liveStatus!==0) {   // 是否直播调试
          if(data.liveStatus!==4) this.btntestlive = true;
        }
        if(data.liveStatus===1) {
          // 是否直播开始，开始直播后rtmp地址：不可修改、开始时间：不可修改、结束时间：可以修改、调试：不可修改
          this.btnlive = true;
        }
        if(data.liveStatus===2){    // 直播结束
          this.btntestlive = true;  // 禁用调试按钮
          this.btnlive = true;      // 禁用rtmp输入框、开始时间输入框
          this.btnliveend = true;   // 禁用结束时间输入框
          this.pickertof = true;    // 禁用提交按钮
        }

        if(data.liveStatus===4) this.testlivename = '结束调试';
        else this.testlivename = '直播调试';

        if(data.liveStatus===0 || data.liveStatus===4){
          this.form.liveStatusname = '立即开始直播';
        }else if(data.liveStatus===1){
          this.form.liveStatusname = '立刻结束直播';
        }else if(data.liveStatus===2){
          this.form.liveStatusname = '直播已结束';
        }
        this.form.liveStatus = data.liveStatus;
        this.dialogVisible = true;
      },
      addresource:function(){
        window.localStorage.setItem('CourseId', null);
        window.localStorage.setItem('saasteachername', '');
        this.$router.push('/AddResource');
      },
      addChapter:function(id){
        window.localStorage.setItem('CourseId',id);
        this.$router.push('/Chapter');
      },
      getcourses(){
        if(this.searchTxt===''){
          var loginParams = { name: this.searchTxt,pgPageNum:1};
          getcourse(loginParams).then(data =>{
            this.ResourceData = data.resList;
            this.Page = data;
            var nowtime = new Date().getTime();
            for(var i=0;i<this.ResourceData.length;i++){
              var d = new Date(this.ResourceData[i].updateTime);
              this.ResourceData[i].updatetime = formatDate(d, 'yyyy-MM-dd hh:mm');
                  if(nowtime<this.ResourceData[i].beginTime){
                    this.ResourceData[i].liveStatus = 0;
                    this.ResourceData[i].liveStatusname = '立即开始直播';
                  }else if(nowtime>this.ResourceData[i].endTime){
                    this.ResourceData[i].liveStatus = 2;
                    this.ResourceData[i].liveStatusname = '直播已结束';
                  }else if(this.ResourceData[i].beginTime===undefined||this.ResourceData[i].endTime===undefined){
                    this.ResourceData[i].liveStatus = 0;
                    this.ResourceData[i].liveStatusname = '立即开始直播';
                  }else{
                    this.ResourceData[i].liveStatus = 1;
                    this.ResourceData[i].liveStatusname = '立刻结束直播';
                  }
              if(this.ResourceData[i].status===1){
                this.ResourceData[i].tofDis = false;
              }else{
                this.ResourceData[i].tofDis = true;
              }
            }
          });
        }
      },
      updresource(data){
        // console.log(data);
        window.localStorage.setItem('saascoursewarehouse',data.warehouse);
        window.localStorage.setItem('saascourseid',data.id);
        window.localStorage.setItem('saascoursename',data.name);
        window.localStorage.setItem('saascoursedesc',data.desc);
        window.localStorage.setItem('saascoursecategory',data.category);
        window.localStorage.setItem('saascoursecover',data.cover);
        window.localStorage.setItem('saascoursecreator',data.creator);
        window.localStorage.setItem('saascourseliveFlag',data.liveFlag);
        window.localStorage.setItem('saascoursedescType',data.descType);
        window.localStorage.setItem('saascourseattach',data.attach);
        // window.localStorage.setItem('saascourselecturer',data.lecturer);
        if(data.lecturerName===undefined){
          window.localStorage.setItem('saascourselecturerName','');
        }else{
          window.localStorage.setItem('saascourselecturerName',data.lecturerName);
        }
        this.$router.push('/UpdResource');
      },
      changedis(data){
        getchapter({course:data.id}).then(data1=>{
          if(data1.success){
            if(data1.resList || data.liveFlag==1){
              for(var i=0;i<this.ResourceData.length;i++){
                if(this.ResourceData[i].id===data.id){
                  var loginParam = '';
                  if(this.ResourceData[i].tofDis){
                    this.ResourceData[i].tofDis = false;
                    loginParam = {id:data.id,desc:'',status:0,name:'',category:'',cover:'',lecturer:''};
                  }else{
                    this.ResourceData[i].tofDis = true;
                    loginParam = {id:data.id,desc:'',status:1,name:'',category:'',cover:'',lecturer:''};
                  }

                  // console.log(data.id);
                  updcourse(loginParam).then(data2 =>{
                    if(data2.success){
                      for(var j=0;j<this.ResourceData.length;j++){
                        if(this.ResourceData[j].id===data.id){
                          if(this.ResourceData[j].tofDis){
                            this.ResourceData[j].tofDis = false;
                          }else{
                            this.ResourceData[j].tofDis = true;
                          }
                        }
                      }
                      this.qwe++;
                    }else{
                      this.openError(data2.resMsg);
                    }
                  })
                }
              }
            }
            else{
              this.$confirm('请添加课程章节?', '提示', {
                confirmButtonText: '添加章节',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                window.localStorage.setItem('CourseId', data.id);
                this.$router.push('Chapter');
              });
            }
          }
        });
      },
      handleCurrentChange(currentPage){
        var loginParams = { name: this.searchTxt,pgPageNum:currentPage};
        getcourse(loginParams).then(data =>{
          this.ResourceData = data.resList;
          this.Page = data;
          var nowtime = new Date().getTime();
          for(var i=0;i<this.ResourceData.length;i++){
            var d = new Date(this.ResourceData[i].updateTime);
            this.ResourceData[i].updatetime = formatDate(d, 'yyyy-MM-dd hh:mm');
                  if(nowtime<this.ResourceData[i].beginTime){
                    this.ResourceData[i].liveStatus = 0;
                    this.ResourceData[i].liveStatusname = '立即开始直播';
                  }else if(nowtime>this.ResourceData[i].endTime){
                    this.ResourceData[i].liveStatus = 2;
                    this.ResourceData[i].liveStatusname = '直播已结束';
                  }else if(this.ResourceData[i].beginTime===undefined||this.ResourceData[i].endTime===undefined){
                    this.ResourceData[i].liveStatus = 0;
                    this.ResourceData[i].liveStatusname = '立即开始直播';
                  }else{
                    this.ResourceData[i].liveStatus = 1;
                    this.ResourceData[i].liveStatusname = '立刻结束直播';
                  }
            if(this.ResourceData[i].status===1){
              this.ResourceData[i].tofDis = false;
            }else{
              this.ResourceData[i].tofDis = true;
            }
          }
        });
      },
      getsearchTxt(){
        var loginParams = { name: this.searchTxt,pgPageNum:0};
        getcourse(loginParams).then(data =>{
          this.ResourceData = data.resList;
          this.Page = data;
          var nowtime = new Date().getTime();
          for(var i=0;i<this.ResourceData.length;i++){
            var d = new Date(this.ResourceData[i].updateTime);
            this.ResourceData[i].updatetime = formatDate(d, 'yyyy-MM-dd hh:mm');
                  if(nowtime<this.ResourceData[i].beginTime){
                    this.ResourceData[i].liveStatus = 0;
                    this.ResourceData[i].liveStatusname = '立即开始直播';
                  }else if(nowtime>this.ResourceData[i].endTime){
                    this.ResourceData[i].liveStatus = 2;
                    this.ResourceData[i].liveStatusname = '直播已结束';
                  }else if(this.ResourceData[i].beginTime===undefined||this.ResourceData[i].endTime===undefined){
                    this.ResourceData[i].liveStatus = 0;
                    this.ResourceData[i].liveStatusname = '立即开始直播';
                  }else{
                    this.ResourceData[i].liveStatus = 1;
                    this.ResourceData[i].liveStatusname = '立刻结束直播';
                  }
            if(this.ResourceData[i].status===1){
              this.ResourceData[i].tofDis = false;
            }else{
              this.ResourceData[i].tofDis = true;
            }
          }
        })
      },
      Ondelcourse(id){
        this.$confirm('此操作将永久删除该课程, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
            }).then(() => {
              delcourse(id).then(data =>{
                if(data.success){
                  if(this.ResourceData.length===1){
                    this.NotDis = true;
                  }
                  for (var i in this.ResourceData) {
                    if(this.ResourceData[i].id===id){
                      this.ResourceData.splice(i,1);
                    }
                  }
                  // this.getsearchTxt();
                }else{
                  this.openError(data.resMsg);
                }
              })
            }).catch(() => {
        });
      },
      show(ev){
        if(ev.keyCode===13){
          this.getsearchTxt();
        }
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.Resource_List_div{
  border-radius: 4px;
  width:300px;
  height:118px;
  border: 1px solid #cdcdcd;
  float: left;
  margin-right: 10px;
  margin-top: 10px;
  padding: 10px;

}
</style>



