<template>
    <div class="BAll">
        <div class="TopDiv">

            <!-- <div class="TopSName" style="border:1px solid red">SaaS</div> -->
            <img class="TopSName" :src="orgLogo"/>

            <el-dropdown trigger="hover" class="TopUName">
                <span class="el-dropdown-link userinfo-inner"><div class="TopImageS"><img class="TopImage" :src="myicon" onerror="this.onerror=null;this.src='./../static/img/avatar.jpg'"/></div>{{name}}</span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="mydata">个人资料</el-dropdown-item>
                    <el-dropdown-item @click.native="updpwd">修改密码</el-dropdown-item>
                    <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
                </el-dropdown-menu>
                    <div style="display:none">{{qwe}}</div>
            </el-dropdown>

            <!-- 机构下拉列表 -->
            <div style="float:right;text-align:right;margin-top:20px;">
                <el-dropdown trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link" style="color:#EFEFEF">
                        <!-- 隐藏机构 -->
                        {{choeseOrg}}<i class="el-icon-caret-bottom" style="margin-left:20px"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown" style="margin-top:10px">
                        <el-dropdown-item v-for="oL in onOrgs" :key="oL.index" :command='oL' style="width:300px;height:30px">
                          <img :src="oL.logoUrl" style="float:left;width:53px;height:30px;object-fit:scale-down;"/>
                          <div style="float:left;line-height:30px;margin-left:5px;width:242px;" class="Over" :title="oL.name"><span>{{oL.name}}</span></div>
                        </el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>

        <div class="MiddleDiv" style="border-top: 1px solid #D1DBE5;">
            <el-menu  class="el-menu-demo" mode="horizontal" :default-active="onRoutes" router style="padding-left:20px;background:#FFFFFF">
                <template v-for="item in items" >
                    <el-menu-item :index="item.index" :key="item.index" style="line-height:40px;height:40px">
                        <i :class="item.icon"></i>{{item.title}}
                    </el-menu-item>
                </template>
            </el-menu>

        </div>

        <div class="Basis_RView" >
            <router-view></router-view>
        </div>

    </div>
</template>

<script>
  import { requestloginOut,getDepartmentAll } from '../api/api';
  export default {
    data() {
        return {
            myicon: window.localStorage.getItem("saaslogoUrl"),
            name: window.localStorage.getItem("saasname"),
            // tof: window.localStorage.getItem("saassuccess"),
            items: [
                // {
                //     icon: 'el-icon-menu',
                //     index: 'Home',
                //     title: '首页'
                // },
                {
                    icon: 'el-icon-picture',
                    index: 'Business',
                    title: '业务中心'
                },
                {
                    icon: 'el-icon-setting',
                    index: 'Setting',
                    title: '设置'
                },
                // {
                //     icon: 'el-icon-message',
                //     index: 'Service',
                //     title: '在线客服'
                // },
                // {
                //     icon: 'el-icon-star-off',
                //     index: 'Mall',
                //     title: '商城'
                // },
                // {
                //     icon: 'el-icon-time',
                //     index: 'Learn',
                //     title: '在线学习'
                // }
            ],
            orgList:'',
            choeseOrg:'',
            orgLogo:'',
            qwe:0,
        }
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }
    },
    mounted(){
      // getDepartmentAll(window.localStorage.getItem("saasoid")).then(data=>{
      //   // console.log(window.localStorage.getItem("saasoid"));
      //   this.$store.commit("updateDeptList", data.resList);
      // });
    },
    methods:{
      openError(text) {
          this.$message({
              showClose: true,
              message: text,
              type: 'error'
          });
      },
      login:function() {
          this.$router.push('/Login');
      },
      logout:function(){
          requestloginOut().then(data => {
              if(data.success){
                  this.$router.push('/Login');
              }else{
                  // this.message = data.resMsg;
                  this.openError(data.resMsg);
              }
          });
      },
      mydata:function(){
          this.$router.push('/UpdMsg');
      },
      updpwd:function(){
          this.$router.push('/UpdPwd');
      },
      handleCommand(oL){
          this.choeseOrg = oL.name;
          window.localStorage.setItem("saasoid",oL.id);
          // console.log(window.localStorage.getItem("saasoid"));
          window.location.reload();
      }
    },
    // updated:function(){
    //     if(window.localStorage.getItem('saasorgList')!==undefined){
    //         this.orgList = JSON.parse(window.localStorage.getItem('saasorgList'));
    //         this.choeseOrg = this.orgList[0].name;
    //         this.qwe++;
    //         return this.orgList;
    //     }
    // },
    computed:{
      onOrgs(){
        var oid = parseInt(window.localStorage.getItem('saasoid'));
        if(window.localStorage.getItem('saasorgList')!==undefined){
            this.orgList = JSON.parse(window.localStorage.getItem('saasorgList'));

            for(var i in this.orgList){
                if(this.orgList[i].id === oid){
                    this.choeseOrg = this.orgList[i].name;
                    this.orgLogo = this.orgList[i].logoUrl;
                }
            }
            this.qwe++;
            return this.orgList;
        }
      },
      onRoutes(){
        var path = this.$route.path.replace('/','');
        if(path === "" ){
            this.$router.push('/Login');
        }
        if(path === "Business"){
            this.$router.push('/Plate');
            path = "Business";
        }
        if(path === "Setting"){
            this.$router.push('/Organization_List');
        }
        if(path === "AddResource" ||
            path === "AddTeacher" ||
            path === "Resource_List" ||
            path === "Teacher_List" ||
            path === "Plate" ||
            path === "Carousel" ||
            path === "Course" ||
            path === "Chapter" ||
            path === "CourseSort" ||
            path === "AddCourseSort" ||
            path === "UpdCarousel" ||
            path === "UpdResource" ||
            path === "UpdCourseSort" ||
            path === "Test_List" ||
            path === "Topic_List" ||
            path === "Test_Detail" ||
            path === "Statistic_List"||
            path === "AddCarousel"){path = "Business"}
        if(path === "Organization_List" ||
        path === "File_List" ||
        path === "Department_List"||
        path === "Personnel_List"||
        path === "Administrator_List"){
            path = "Setting"
        }
        return path;
      }
    }
  }
</script>



