<template>
  <div class="MyData_Div">
    <!-- <div class="W95"></div> -->
    <div >
      <div class="TopName W95">{{title}}</div>
    </div>
    <div class="TopLine"></div>
    <!--<div >-->
      <!--<div class="W95">-->
        <!--<el-button type="text" id="Msg" @click="GoCourse">课程管理</el-button>-->
        <!--<el-button type="text" id="Icon" @click="GoCourseSort">课程分类管理</el-button>-->
      <!--</div>-->
    <!--</div>-->
    <div class="TopLine"></div>
    <div class="FirstP_Content">
      <router-view></router-view>
    </div>

  </div>
</template>


<script>
  export default {
    data() {
      return {
        value1: true,
        ResourceData:[],
      }
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }
    },
    computed:{
      title(){
        if(this.$route.path == '/Course') return '课程管理';
        if(this.$route.path == '/CourseSort') return '课程分类';
      }
    },
    methods:{
      GoCourse: function(){
        this.$router.push('/Course');
      },
      GoCourseSort: function(){
        this.$router.push('/CourseSort');
      }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



