<template>
    <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95">考试管理</div>
      </div>
      <div class="TopLine"></div>
      <div class="TopButtonT W95">
        <span class="TopButtonLeft">
          <el-button type="primary" @click="AddExam" style="width:100px">创建考试</el-button>
        </span>
        <div class="TopButtonRight4">
          <input class="TopButtonRightInput" type="text"  v-model="searchTxt" placeholder="考试名称" @input="searchExam1" @keyup.enter="searchExam2"/>
          <label class="el-icon-search TopButtonRightSearch" @click=""></label>
          <el-button type="default" style="padding: 5px 10px;" @click="searchExam2">搜索</el-button>
        </div>
      </div>
      <div id="TableDiv" class="W95 Teacher_List_Table">
        <div style="width: 100%;height:calc(100% - 20px);overflow-y: auto;border:1px solid #cdcdcd;"
        v-loading="Gloading"
        element-loading-text="加载中。。。"
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)">
          <div style="width:100%;height:40px;line-height:40px;text-align: center">
            <div class="table_header" style="float:left;width:calc(20% - 5px);height:100%;border-bottom:1px solid #cdcdcd;">考试名称</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">考试开始时间</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">考试结束时间</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">是否开放</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">当前状态</div>
            <div class='ListBorder'></div>
            <div class="table_header" style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">操作</div>

            <span v-for="item in ExamList.resList" v-if="hackReset">
              <div style="width:calc(20% - 13px);height:100%;float:left;padding:0 4px;border-bottom:1px solid #cdcdcd;" class="Over">{{item.name}} {{item.id}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">{{item.begintime}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">{{item.endtime}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">
                <el-switch @change="OpenExamStatus(item)"
                  v-model="item.statusBoolean"
                  active-color="#13ce66"
                  inactive-color="#ff4949">
                </el-switch>
              </div>
              <div class='ListBorder'></div>
              <div style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">{{item.currentStatus}}</div>
              <div class='ListBorder'></div>
              <div style="float:left;width:16%;height:100%;border-bottom:1px solid #cdcdcd;">
                <el-button type="text" @click="EditExam(item)">编辑</el-button>
                <el-button type="text" @click="DelExam(item)">删除</el-button>
              </div>
            </span>
          </div>
          <not-content v-if="NotDis"></not-content>
        </div>
      </div>
      <el-pagination
        style="margin-right:20px;float:right;"
        :page-size="ExamList.pgLimit"
        @current-change="handleCurrentChange"
        layout="prev, pager, next, jumper"
        :total="ExamList.pgTotalCount">
      </el-pagination>
    </div>
</template>

<script>
  import { getExamList,openExam,delExam } from '../../../api/api';
  import NotContent from '../Modules/NotContent';
  import { formatDate } from '../../../util/util';
  export default {
    data(){
      return {
        NotDis:false,
        searchTxt:'',
        Gloading:true,
        value: true,
        ExamList: [],
        hackReset: false,
        Page:'',
        pgLimit: 30
      }
    },
    components:{
        NotContent
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }
    },
    mounted(){
      this.getExam({pgPageNum:1});
    },
    methods:{
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      getExam(params){
        getExamList(params).then(data=>{
          if(data.success && data.resList) {
            this.ExamList = data;
            this.ExamList.resList.forEach(item=>{
              if(item.status==0) item.statusBoolean = true;
              else item.statusBoolean = false;
              // item.begintime = formatDate( new Date(item.beginTime),'yyyy-MM-dd hh:mm');
              item.begintime = formatDate( new Date(item.beginTime),'yyyy-MM-dd');
              item.endtime = formatDate( new Date(item.endTime),'yyyy-MM-dd');

              var now = new Date().getTime();
              if(now<item.beginTime) item.currentStatus = '未开始';
              if(item.beginTime<now && now<item.endTime) item.currentStatus = '正在进行';
              if(now>item.endTime) item.currentStatus = '已结束';
              this.hack();
            });
            this.Gloading = false;
          }
          else{
            this.Gloading = false;
            this.NotDis = true;
          }
        });
      },
      hack(){
        this.hackReset = false; // hack方法强制刷新组件
        this.$nextTick(() => {
          this.hackReset = true;
        })
      },
      formatDate(date,fmt) {
        if(/(y+)/.test(fmt)){
          fmt = fmt.replace(RegExp.$1,(date.getFullYear()+'').substr(4-RegExp.$1.length));
        }
        let o = {
          'M+':date.getMonth() + 1,
          'd+':date.getDate(),
          'h+':date.getHours(),
          'm+':date.getMinutes(),
          's+':date.getSeconds()
        };

        // 遍历这个对象
        for(let k in o){
          if(new RegExp(`(${k})`).test(fmt)){
            // console.log(`${k}`)
            // console.log(RegExp.$1)
            let str = o[k] + '';
            fmt = fmt.replace(RegExp.$1,(RegExp.$1.length===1)?str:padLeftZero(str));
          }
        }
        return fmt;
      },
      OpenExamStatus(item){
        // console.log(item);
        // console.log(item.status,item.status?0:1);
        openExam({id:item.id, status:item.status?0:1}).then(data=>{
          console.log(data);
          if(data.success){
            this.openSuccess('开启成功');
            this.hack();
          }
          else this.openError(data.resMsg);
        });
      },
      AddExam(){
        this.$router.push('AddExam');
      },
      EditExam(item){
        window.localStorage.setItem("saasExamid",item.id);
        this.$router.push('UpdExam');
      },
      DelExam(item){
        this.$confirm('此操作将永久删除该考试, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          delExam(item.id).then(data=>{
            if(data.success){
              this.openSuccess('删除成功！');

              if(this.ExamList.resList.length==1){
                this.NotDis = true;
              }
              for(var i in this.ExamList.resList){
                if(this.ExamList.resList[i].id==item.id){
                  this.ExamList.resList.splice(i,1);
                }
              }
            }else{
              this.openError(data.resMsg);
            }
          });
        }).catch(() => {});
      },
      searchExam1(){
        if(this.searchTxt==='') this.getExam({name:this.searchTxt});
      },
      searchExam2(){
        if(this.searchTxt!='') this.getExam({name:this.searchTxt});
        else this.openError('请先输入考试名称再进行搜索');
      },
      handleCurrentChange(currentPage){
        this.getExam({pgPageNum:currentPage});
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



