<template>
  <div style="width:100%;height:100%;">
    <div>
      <div class="TopName W95">试卷管理</div>
    </div>
    <div class="TopLine"></div>
    <div class="TopButtonT W95">
      <span class="TopButtonLeft">
        <el-button type="primary" @click="AddPaper" style="width:100px">添加试卷</el-button>
      </span>
      <div class="TopButtonRight4">
        <input class="TopButtonRightInput" type="text" v-model="searchTxt" placeholder="试卷名称" @input="searchExam1" @keyup.enter="searchExam2"/>
        <label class="el-icon-search TopButtonRightSearch" @click="searchExam2"></label>
        <el-button type="default" style="padding: 5px 10px;" @click="searchExam2">搜索</el-button>
      </div>
    </div>
    <div id="TableDiv" class="W95 Teacher_List_Table">
      <div style="width: 100%;height:calc(100% - 20px);overflow-y: auto;border:1px solid #cdcdcd;"
      v-loading="Gloading"
      element-loading-text="加载中。。。"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)">
        <div style="width:100%;height:40px;line-height:40px;text-align: center">
          <div class="table_header" style="float:left;width:calc(40% - 3px);height:100%;border-bottom:1px solid #cdcdcd;">试卷名称</div>
          <div class='ListBorder'></div>
          <div class="table_header" style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">考试时长(分)</div>
          <div class='ListBorder'></div>
          <div class="table_header" style="float:left;width:25%;height:100%;border-bottom:1px solid #cdcdcd;">总分值</div>
          <div class='ListBorder'></div>
          <div class="table_header" style="float:left;width:20%;height:100%;border-bottom:1px solid #cdcdcd;">操作</div>

          <span v-for="item in PaperList.resList">
            <div style="width:calc(40% - 11px);height:100%;float:left;padding:0 4px;border-bottom:1px solid #cdcdcd;" class="Over">{{item.name}}</div>
            <div class='ListBorder'></div>
            <div style="float:left;width:15%;height:100%;border-bottom:1px solid #cdcdcd;">{{item.duration}}</div>
            <div class='ListBorder'></div>
            <div style="float:left;width:25%;height:100%;border-bottom:1px solid #cdcdcd;">{{item.total}}</div>
          <div class='ListBorder'></div>
          <div style="float:left;width:20%;height:100%;border-bottom:1px solid #cdcdcd;">
            <el-button type="text" @click="EditPaper(item)">编辑</el-button>
            <el-button type="text" @click="DelPaper(item)">删除</el-button>
          </div>
          </span>
        </div>
        <not-content v-if="NotDis"></not-content>
      </div>
    </div>
    <el-pagination
      style="margin-right:20px;float:right;"
      :page-size="PaperList.pgLimit"
      @current-change="handleCurrentChange"
      layout="prev, pager, next, jumper"
      :total="PaperList.pgTotalCount">
    </el-pagination>
  </div>
</template>

<script>
import { getPaperList,delPaper } from '../../../api/api';
import NotContent from '../Modules/NotContent';
import { formatDate } from '../../../util/util';
export default {
  data() {
    return {
      NotDis:false,
      tableData: [],
      data: [],
      mobile:'',
      Page:'',
      tof:'',
      searchTxt:'',
      Gloading:true,
      dialogVisible:false,
      dialogVisible1:false,
      changTestid:'',
      changTestname:'',
      name:'',
      PaperList: []
    }
  },
  components:{
    NotContent
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }else{

    }
  },
  mounted(){
    this.getPaper({pgPageNum:1});
  },
  methods:{
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    getPaper(params){
      getPaperList(params).then(data=>{
        // console.log(data);
        if(data.success && data.resList!=''){
          this.PaperList = data;
          this.Gloading = false;
        }
      });
    },
    AddPaper(){
      window.localStorage.setItem("saasaddPaper",'添加试卷');
      this.$router.push('AddPaper');
    },
    EditPaper(item){
      window.localStorage.setItem("saasaddPaper",'编辑试卷');
      window.localStorage.setItem("saasPaperid",item.id);
      this.$router.push('UpdPaper');
    },
    DelPaper(item){
      this.$confirm('此操作将永久删除该试卷, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        delPaper(item.id).then(data=>{
          if(data.success){
            this.openSuccess('删除成功！');

            if(this.PaperList.resList.length==1){
              this.NotDis = true;
            }
            for(var i in this.PaperList.resList){
              if(this.PaperList.resList[i].id==item.id){
                this.PaperList.resList.splice(i,1);
              }
            }
          }else{
            this.openError(data.resMsg);
          }
        });
      }).catch(() => {});
    },
    searchExam1(){
      if(this.searchTxt==='') this.getPaper({name:this.searchTxt,pgPageNum:1});
    },
    searchExam2(){
      if(this.searchTxt!='') this.getPaper({name:this.searchTxt,pgPageNum:1});
      else this.openError('请先输入试卷名称再进行搜索');
    },
    handleCurrentChange(currentPage){
      this.getPaper({pgPageNum:currentPage});
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



