<template>
    <div class="login-wrap">
        <div class="ms-title">Saas后台管理系统</div>
        <div class="ms-login">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="0px" class="demo-ruleForm"
            v-loading="Gloading"
            element-loading-text="登录中。。。"
            element-loading-spinner="el-icon-loading"
            element-loading-background="rgba(0, 0, 0, 0.8)">
                <el-form-item prop="username">
                    <el-input v-model="ruleForm.username" placeholder="输入手机号" style="width:200px;"></el-input>
                    <el-button style="width:95px;font-size:12px;height:36px" @click="getCode" :disabled="tof">{{count}}</el-button>
                </el-form-item>
                <el-form-item prop="password">
                    <el-input type="password" placeholder="密码/验证码" v-model="ruleForm.password" @keyup.enter.native="submitForm('ruleForm')" ></el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button type="primary" @click.native.prevent="submitForm">登录</el-button>
                    <!-- <button @click="qwer">qwe</button> -->
                </div>
                <p style="font-size:12px;line-height:30px;color:red;text-align: center;">{{message}}</p>
            </el-form>
        </div>
    </div>
</template>

<script>
  import { requestLogin,requestCode,getfile } from '../api/api';
  import { SaveMsg,ClearMsg } from '../util/util';
    export default {
        data: function(){
            return {
                Gloading:false,
                message: '',
                count: '发送验证码',
                // time: 60,
                tof: false,
                ruleForm: {
                    username: '',
                    password: ''
                },
                rules: {
                    username:[{validator:(rule,value,callback)=>{
                        if(/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(14[0-9]{1})|(16[0-9]{1})|(17[0-9]{1})|)+\d{11})$/.test(value) == false){
                            callback(new Error("请输入正确的手机号"));
                        }else{
                            callback();
                        }
                    }, trigger:'blur'}],
                    password:[{validator:(rule,value,callback)=>{
                        if(value.length < 4 || value.length === 5 || value.length > 16){
                            callback(new Error("请输入正确的验证码或密码"));
                        }else{
                            callback();
                        }
                    }, trigger:'blur'}]
                },
            }
        },
        beforeCreate:function(){
            window.localStorage.setItem('saasLoginTrueOrFalse','false');

        },
        // socket: {
        //     connect: function(){
        //         console.log('socket connected')
        //     },
        //     message: function(msg){
        //         console.log('socket message' + msg)
        //     }
        // },
        methods: {

            // qwer(){
            //     if ("WebSocket" in window)
            //     {
            //     console.log("您的浏览器支持 WebSocket!");

            //     // 打开一个 web socket
            //     var ws = new WebSocket("wss://120.132.23.37:8848");

            //     ws.onopen = function()
            //     {
            //         // Web Socket 已连接上，使用 send() 方法发送数据
            //         ws.send("发送数据");
            //         console.log("数据发送中...");
            //     };

            //     ws.onmessage = function (evt)
            //     {
            //         var received_msg = evt.data;
            //         console.log("数据已接收...");
            //     };

            //     ws.onclose = function()
            //     {
            //         // 关闭 websocket
            //         console.log("连接已关闭...");
            //     };
            //     }

            //     else
            //     {
            //     // 浏览器不支持 WebSocket
            //     console.log("您的浏览器不支持 WebSocket!");
            //     }
            // },
            submitForm(ev) {
                const self = this;
                self.Gloading = true;
                self.$refs.ruleForm.validate((valid) => {
                    if (valid) {
                        this.logining = true;
                        var loginParams = { mobile: this.ruleForm.username, code: this.ruleForm.password };
                        var jse = new this.$jsEncrypt.JSEncrypt();
                        requestLogin(jse,loginParams).then(data => {
                            self.Gloading = false;
                            // console.log(data);
                            if(data.success){
                                // console.log(data);
                                if(data.nickName === undefined){
                                    data.nickName = '';
                                }
                                //成功之后把信息都存入本地缓存
                                SaveMsg(data);
                                window.localStorage.setItem('saasLoginTrueOrFalse','true');
                                self.$router.push('/FirstP_List');
                                this.message = "";
                            }else{
                                this.message = data.resMsg;
                                //失败之后把本地缓存清除掉
                                ClearMsg();
                            }
                        }).catch(err =>{this.message = '请求失败，请稍后重试！'});
                    } else {
                        self.Gloading = false;
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            getCode(){
                if(/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(14[0-9]{1})|(16[0-9]{1})|(17[0-9]{1})|)+\d{8})$/.test(this.ruleForm.username) == true && this.ruleForm.username.length === 11){
                        var loginParams = { mobile: this.ruleForm.username};
                        requestCode(loginParams).then(data => {
                            if(data.success){
                                this.message = "已获取短信验证码";
                                this.tof = true;
                                let that = this;
                                timeout();
                                function timeout(){
                                    if(that.count==='发送验证码'){
                                        that.count = 60;
                                        timeout();
                                    }else if(that.count === 1){
                                        that.count='发送验证码'
                                        that.tof = false;
                                    }else {
                                        setTimeout(function() {
                                            that.count--;
                                            timeout();
                                        }, 1000);
                                    }
                                }
                            }else{
                                this.message = data.resMsg;
                            }
                        });

                }
            }
        }
    }
</script>



