
<template>
  <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95">机构管理</div>
      </div>
      <div class="TopLine"></div>
        <br/>
        <br/>
      <el-form ref="form" :model="form" label-width="100px" id="form" method="post" >
        <el-form-item label="企业名称">
          <el-input v-model="form.name" style="width: 400px;" @change="verification" :maxlength="30"></el-input>
        </el-form-item>
        <br/>
        <el-form-item label="企业简介" style="width: 500px;">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入内容" @change="verification"
            v-model="form.desc">
          </el-input>
        </el-form-item>
        <br/>
        <el-form-item label="企业Logo">
          <!-- <div class="UploadImage_Div">
              <img src="./../../../../static/img/img.jpg" />
              <div>
                  <div style="width:230px;height:100px;margin-top:10px;padding-left: 20px;">*仅支持JPG、PNG格式的图片文件 文件大小小于5M</div>
                  <div style="width:230px;height:30px;padding-left: 20px;position: relative;">
                      <el-button size="small" style="margin-left:20px;width:80px">本地上传</el-button>
                      <input type="file" class="UIfile" title="点击选择本地文件"/>
                  </div>
              </div>
          </div> -->
          <!-- <div class="UploadImage_Div">
            <img id="myicon" :src="form.logoUrl" onerror="this.src='./../../../../static/img/avatar.jpg'"/>
            <div>
              <div style="width:230px;height:100px;margin-top:10px;padding-left: 20px;">*仅支持JPG、PNG格式的图片文件 文件大小小于5M</div>
              <div style="width:230px;height:30px;padding-left: 20px;position: relative;">
                  <el-button size="small" style="margin-left:20px;width:80px">本地上传</el-button>
                  <input id="myfile" type="file" class="UIfile" title="点击选择本地文件" @change="getFile($event)" accept="image/jpeg,image/png"/>
              </div>
            </div>
          </div> -->
          <div class="UploadImage_Div">
                <canvas id="tp" v-if="icontof"></canvas>
                <img v-else id="myicons" :src="form.logoUrl" onerror="this.onerror=null;this.src='./../../../static/img/avatar.jpg'" style="object-fit:scale-down;"/>
                <div>
                    <div style="width:230px;height:100px;margin-top:10px;padding-left: 20px;">*仅支持JPG、PNG格式的图片文件 文件大小小于5M</div>
                    <div style="width:230px;height:30px;margin-left:20px;position: relative;">
                        <el-button size="small" style="width:80px">本地上传</el-button>
                        <input id="myfile" type="file" name="files" multipart class="UIfile" title="点击选择本地文件" @change="getFile($event)" accept="image/jpeg,image/png"/>
                        <div style="display:none">{{qwe}}</div>
                    </div>
                </div>
            </div>
        </el-form-item>
        <br/>
        <el-form-item label="" >
          <el-button type="primary"style="width:120px" @click="Onupdorganization" :disabled="tof">提交</el-button>
        </el-form-item>
      </el-form>
    <!-- <div class="MyData_Button"><el-button type="primary" size="large">保存</el-button></div> -->
</div>
</template>

<script>
  import { getorganization,updorganization,uploadImg } from '../../../api/api';
  // import UploadImage from "./../module/UploadImage.vue";
export default {
  data () {
    return {
      form: {
        name: '',
        desc: '',
        logoUrl:''
      },
      myicon:'',
      files: '',
      qwe:'',
      imageUrl: '',
      icontof:false,
      ie9tof:navigator.userAgent.indexOf("MSIE 9.0")>0,
      tof:true,
      JUrl:window.localStorage.getItem('saasJUrl')
    }
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }else{
      getorganization().then(data =>{
        // console.log(data);
        this.form.name = data.name;
        this.form.desc = data.desc;
        this.form.logoUrl = data.logoUrl;
      })
    }
  },
  methods:{
    Onupdorganization(){
      var that = this;
      if(that.files===""){
        updorganization(that.form).then(data =>{
                if(data.success){
                  that.openSuccess('修改成功');
                  var orgList = JSON.parse(window.localStorage.getItem('saasorgList'));
                  var oid = parseInt(window.localStorage.getItem('saasoid'));
                  for(var i in orgList){
                    if(orgList[i].id===oid){
                      orgList[i].name = that.form.name;
                      orgList[i].desc = that.form.desc;
                      window.localStorage.setItem('saasorgList',JSON.stringify(orgList));
                      window.location.reload();
                    }
                  }
                }else{
                  that.openError(data.resMsg);
                }
              })
      }else{
        var options = {
          url:that.JUrl,
          type:'post',
          enctype:'multipart/form-data',
          success:that.successRes
        }
        that.Jq("#form").ajaxSubmit(options);
      }
    },
    successRes(jsondata){
      var data = JSON.parse(jsondata);
      if(data.resMsg === "success"){
          this.form.logoUrl = data.url;
              // console.log(this.form);
              updorganization(this.form).then(data =>{
                // console.log(data);
                if(data.success){
                  this.openSuccess('修改成功');
                  var orgList = JSON.parse(window.localStorage.getItem('saasorgList'));
                  var oid = parseInt(window.localStorage.getItem('saasoid'));
                  for(var i in orgList){
                    if(orgList[i].id===oid){
                      orgList[i].name = this.form.name;
                      orgList[i].desc = this.form.desc;
                      orgList[i].logoUrl = this.form.logoUrl;
                      window.localStorage.setItem('saasorgList',JSON.stringify(orgList));
                      window.location.reload();
                    }
                  }
                }else{
                  this.openError(data.resMsg);
                }
              })
          }else{
            this.openError(data.msg);
          }
    },
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    getFile(objs) {
      this.files = '123';
        if(this.ie9tof){
          var that = this;
          that.icontof = true;
          setTimeout(function() {
            var obj = document.getElementById("myfile");
            obj.select(); obj.blur();
            that.files = document.selection.createRange().text;
            document.selection.empty();
            document.getElementById("tp").style.filter="progid:DXImageTransform.Microsoft.AlphaImageLoader(sizingMethod='scale',src='"+that.files+"')";
            // console.log(that.files);
          if(that.form.name.length !== '' && that.form.desc.length !== '' && that.files !== ''){
            that.tof = false;
          }else{
            that.tof = true;
          }
        }, 50);
      }else{
        this.icontof = false;
        var myicons = document.getElementById("myicons");
        this.files = objs.target.files[0];
        // console.log(this.files);
        var reader = new FileReader();
        reader.readAsDataURL(this.files);
        reader.onload = function (e) {
            myicons.src = this.result;
        }
        if(this.form.name.length !== '' && this.form.desc.length !== '' && this.files !== ''){
          this.tof = false;
        }else{
          this.tof = true;
        }
      }
    },
    verification(){
      if(this.form.name.length !== 0 && this.form.desc.length !== 0){
        this.tof = false;
      }else{
        this.tof = true;
      }
    }
  }
}
</script>



