<template>
    <div style="width:calc(100% - 10px);height:calc(100% - 50px);margin:5px;border:1px solid #cdcdcd">
        <div style="width:100%;height:120px;border-bottom: 1px solid #cdcdcd">
            <div style="width:20%;height:40px;float:left;line-height:40px;padding-left:20px">{{choeseT.name}}</div>
            <div style="width:75%;height:40px;float:right;">
                <el-button style="width:100px;margin-top:10px;float:right;margin-right:20px" @click="GoDelCourseSort" :disabled="Toftree">删除分类</el-button>
                <el-button style="width:100px;margin-top:10px;float:right;margin-right:10px" @click="GoUpdCourseSort()" :disabled="Toftree">编辑分类</el-button>
                <el-button style="width:120px;margin-top:10px;float:right;" @click="GoAddCourseSort()" :disabled="Tofchilder">添加子分类</el-button>
                <el-button style="width:120px;margin-top:10px;float:right;" @click="GoAddCourseSorts(1)" :disabled="Toftree">添加同级分类</el-button>
                <el-button style="width:120px;margin-top:10px;float:right;" @click="GoAddCourseSorts(0)" >添加一级分类</el-button>
            </div>
            <div style="width:20%;height:40px;float:left;line-height:40px;padding-left:20px;font-weight: bold;">分类描述</div>
            <div style="width:calc(100% - 40px);height:40px;float:left;line-height:40px;padding-left:20px;padding-right:20px;overflow: hidden;">
                {{choeseT.desc}}
            </div>
        </div>
        <div style="width:100%;height:calc(100% - 120px);">
            <div style="width:200px;height:calc(100% - 20px);margin:10px;float:left;border:1px solid #cdcdcd;overflow-y: auto;">
              <div style="width:100%;" v-for="One in data">
                <div style="width:100%;line-height:40px;text-indent: 5px;cursor: pointer;" v-bind:class="{ asd: One.rofShadow }" @click="TreeClick(One.id,One.name,One.desc,One.parent,One.oid)">
                  <span v-if="One.resList!==undefined">
                    <i v-if="One.tofDis" class="el-icon-minus"></i>
                    <i v-else class="el-icon-plus"></i>
                  </span>
                  {{One.name}}
                </div>
                <div style=
                       "width:100%;" v-if="One.tofDis" v-for="Two in One.resList">
                  <div style="width:100%;height:40px;line-height:40px;text-indent: 15px;cursor: pointer;" v-bind:class="{ asd: Two.rofShadow }" @click="TreeClick(Two.id,Two.name,Two.desc,Two.parent,Two.oid)">
                    <span v-if="Two.resList!==undefined">
                      <i v-if="Two.tofDis" class="el-icon-minus"></i>
                      <i v-else class="el-icon-plus"></i>
                    </span>
                    {{Two.name}}
                  </div>
                  <div style="width:100%;" v-if="Two.tofDis" v-for="Three in Two.resList">
                    <div style="width:100%;height:40px;line-height:40px;text-indent: 60px;" v-bind:class="{ asd: Three.rofShadow }" @click="TreeClick(Three.id,Three.name,Three.desc,Three.parent,Three.oid)">{{Three.name}}</div>
                  </div>
                </div><div style="display:none">{{qwe}}</div>
              </div>
                <!-- <el-tree :data="data" :props="defaultProps" @node-click="handleNodeClick" style="height:100%;overflow-y: auto"></el-tree>  -->
            </div>
            <div style="width:calc(100% - 240px);height:calc(100% - 20px);border:1px solid #cdcdcd;float:right;margin-top:10px;margin-right:10px;overflow-y: auto">
                <el-table :data="tableData" border style="width: 100%" key="tableData" v-if="tof">
                    <el-table-column prop="sort" label="序列" width="150"></el-table-column>
                    <el-table-column prop="name" label="姓名" width="150"></el-table-column>
                    <el-table-column prop="desc" label="分类描述"></el-table-column>
                </el-table>
                <el-table :data="tableData" border style="width: 100%" v-else>
                    <el-table-column prop="id" label="序列" width="150"></el-table-column>
                    <el-table-column prop="name" label="姓名" width="150"></el-table-column>
                    <el-table-column label="分类描述">
                      <template slot-scope="scope">
                        <div v-html="scope.row.desc" style="max-height:140px;overflow-y: auto"></div>
                      </template>
                    </el-table-column>
                    <el-table-column label="操作" width="150">
                      <template slot-scope="scope">
                        <el-button type="text" @click="delcaurse(scope.row.id)" size="small">删除</el-button>
                      </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
    </div>
</template>

<script>
import { getcategory,getOnecategorys,delcategory,getOnecourse,delcourse } from '../../../../api/api';
export default {
    data() {
      return {
        choeseT:{
          id:'',
          name:'请先选择分类',
          desc:'请先选择分类',
        },
        qwe:0,
        data: [],
        Tofchilder:true,
        Toftree:true,
        tableData: [],
        defaultProps: {
          children: 'resList',
          label: 'name'
        },
        tof:true,
      };
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }else{
          getcategory().then(data =>{
            // console.log(data);
            if(data.success){
              if(data.resList!==undefined){
                this.data = data.resList;
                for(var i=0;i<this.data.length;i++){
                  if(this.data[i].resList!==undefined){
                    this.data[i].tofDis = false;
                    for(var j=0;j<this.data[i].resList.length;j++){
                      if(this.data[i].resList[j].resList!==undefined){
                        this.data[i].resList[j].tofDis = false;
                      }
                    }
                  }
                }
                for(var i=0;i<this.data.length;i++){
                  if(this.data[i].resList!==undefined){
                    this.data[i].rofShadow = false;
                    for(var j=0;j<this.data[i].resList.length;j++){
                      if(this.data[i].resList[j].resList!==undefined){
                        this.data[i].resList[j].rofShadow = false;
                        for(var k=0;k<this.data[i].resList[j].resList.length;k++){
                          this.data[i].resList[j].resList[k].rofShadow = false;
                        }
                      }
                    }
                  }
                }
              }
            }else{
              this.openError(data.resMsg);
            }
          })
        }
    },
    methods: {
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      //编辑分类
      GoUpdCourseSort(){
       if(this.choeseT.id!==''){
        window.localStorage.setItem('saascategorydesc',this.choeseT.desc);
        window.localStorage.setItem('saascategoryid',this.choeseT.id);
        window.localStorage.setItem('saascategoryname',this.choeseT.name);
        this.$router.push('/UpdCourseSort');
        }else{
          this.openError('请选择分类');
        }
      },
      GoAddCourseSort(){
       if(this.choeseT.id!==''){
         if(this.data!=undefined){
          for(var i=0;i<this.data.length;i++){
            if(this.data[i].id===this.choeseT.id){
                window.localStorage.setItem('saascategoryid',this.choeseT.id);
                window.localStorage.setItem('saascategoryname',this.choeseT.name);
              this.$router.push('/AddCourseSort');
            }
            if(this.data[i].resList!==undefined){
              for(var j=0;j<this.data[i].resList.length;j++){
                if(this.data[i].resList[j].id===this.choeseT.id){
                    window.localStorage.setItem('saascategoryid',this.choeseT.id);
                    window.localStorage.setItem('saascategoryname',this.choeseT.name);
                  this.$router.push('/AddCourseSort');
                }
              }
            }
          }
        }
       }else{
          this.openError('请选择分类');
        }
      },
      //添加分类
      GoAddCourseSorts(num){
        if(num===1){
            window.localStorage.setItem('saascategoryid',this.choeseT.id);
            window.localStorage.setItem('saascategoryname',this.choeseT.name);
            window.localStorage.setItem('saascategoryparent',this.choeseT.parent);
            window.localStorage.setItem('saascategorytof',num);
        }else{
            window.localStorage.setItem('saascategorytof',num);
        }
        window.localStorage.setItem('saascategory','添加分类');
        this.$router.push('/AddCourseSortT');
      },
      //删除分类
      GoDelCourseSort(){
        // console.log(this.choeseT.oid);
        if(this.choeseT.oid===0){
          this.openError('该分类无权限删除');
        }else{
          this.$confirm('此操作将永久删除该分类, 是否继续?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
              }).then(() => {
                if(this.choeseT.id!==''){
                  delcategory(this.choeseT.id).then(data =>{
                    if(data.success){
                    this.Tofchilder = true;
                    this.Toftree = true;
                    this.qwe++;
                    for (var i in this.data) {
                      if(this.data[i].id===this.choeseT.id){
                        this.data.splice(i,1);
                      }
                      for(var j in this.data[i].resList){
                        if(this.data[i].resList[j].id===this.choeseT.id){
                          this.data[i].resList.splice(j,1);
                        }
                        for(var k in this.data[i].resList[j].resList){
                          if(this.data[i].resList[j].resList[k].id===this.choeseT.id){
                            this.data[i].resList[j].resList.splice(k,1);
                          }
                        }
                      }
                    }
                    }else{
                      this.openError(data.resMsg);
                    }
                  });
                }else{
                  this.openError('请选择分类');
                }
              }).catch(() => {
          });
        }
      },
      //分类下的课程删除
      delcaurse(id){
        delcourse(id).then(data =>{
          if(data.success){
            getOnecourse(this.choeseT.id).then(data =>{
                      // console.log(data);
                      if(data.success){
                        this.tof = false;
                      }else{
                        this.openError(data.resMsg);
                      }
                      this.tableData = data.resList;
                    })
          }else{
            this.openError(data.success);
          }
        })
      },
      //点击分类节点时获取所有的子节点并展开
      TreeClick(id,name,desc,parent,oid){
        this.Tofchilder = false;
        this.Toftree = false;
        if(desc===undefined){
          desc = '暂无描述';
        }
        this.choeseT.parent = parent;
        this.choeseT.id = id;
        this.choeseT.name = name;
        this.choeseT.desc = desc;
        this.choeseT.oid = oid;

        getOnecategorys(id).then(data =>{
          // console.log(data);
          if(data.success){
            this.tof = true;
            if(data.resList!=undefined){
              this.tableData = data.resList;
              for(var i=0;i<this.tableData.length;i++){
                if(this.tableData[i].desc===undefined){
                  this.tableData[i].desc = '该分类暂无描述';
                }
              }
            }
            else{
              this.tableData = [];
            }
          }else{
            this.openError(data.resMsg);
          }
        })
        // console.log(this.data);
        for(var i=0;i<this.data.length;i++){
          if(this.data[i].id===id){
            if(this.data[i].tofDis){
              this.data[i].tofDis = false;
            }else{
              this.data[i].tofDis = true;
            }
            break;
          }else{
            if(this.data[i].resList!==undefined){
              for(var j=0;j<this.data[i].resList.length;j++){
                if(this.data[i].resList[j].id===id){
                  if(this.data[i].resList[j].tofDis){
                    this.data[i].resList[j].tofDis = false;
                  }else{
                    this.data[i].resList[j].tofDis = true;
                  }
                  break;
                }
              }
            }
          }
        }
        for(var i=0;i<this.data.length;i++){
            if(this.data[i].id===id){
              if(this.data[i].rofShadow){
                this.data[i].rofShadow = false;
                this.Tofchilder = true;
                this.Toftree = true;
              }else{
                this.data[i].rofShadow = true;
              }
            }else{
                this.data[i].rofShadow = false;
            }
          if(this.data[i].resList!==undefined){
            for(var j=0;j<this.data[i].resList.length;j++){
                if(this.data[i].resList[j].id===id){
                  if(this.data[i].resList[j].rofShadow){
                    this.data[i].resList[j].rofShadow = false;
                    this.Tofchilder = true;
                    this.Toftree = true;
                  }else{
                    this.data[i].resList[j].rofShadow = true;
                  }
                }else{
                  this.data[i].resList[j].rofShadow = false;
                }
              if(this.data[i].resList[j].resList!==undefined){
                for(var k=0;k<this.data[i].resList[j].resList.length;k++){
                  if(this.data[i].resList[j].resList[k].id===id){
                    this.Tofchilder = true;
                    getOnecourse(id).then(data =>{
                      // console.log(data);
                      if(data.success){
                        this.tof = false;
                      }else{
                        this.openError(data.resMsg);
                      }
                      this.tableData = data.resList;
                    })

                    if(this.data[i].resList[j].resList[k].rofShadow){
                      this.data[i].resList[j].resList[k].rofShadow = false;
                      this.Tofchilder = true;
                      this.Toftree = true;
                    }else{
                      this.data[i].resList[j].resList[k].rofShadow = true;
                    }
                  }else{
                    this.data[i].resList[j].resList[k].rofShadow = false;
                  }
                }
              }
            }
          }
        }
        this.qwe++;
      }
    }
  };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.asd{
  background: #dcdcdc;
}
</style>



