
<template>
  <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95">参数设置</div>
      </div>
      <div class="TopLine"></div>
        <br/>
        <br/>
      <el-form ref="form" :model="form" label-width="100px" id="form" method="post" >
        <el-form-item label="rtmp地址">
          <el-input v-model="form.livepath" style="width: 400px;" @change="verification" :maxlength="50"></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-button type="primary"style="width:120px" @click="OnupdParam" :disabled="tof">提交</el-button>
        </el-form-item>
      </el-form>
  </div>
</template>

<script>
  import { updorganization,getorganization } from '../../../api/api';
  export default {
    data () {
      return {
        form: {
          livepath: ''
        },
        files: '',
        qwe:'',
        imageUrl: '',
        ie9tof:navigator.userAgent.indexOf("MSIE 9.0")>0,
        tof:true
      }
    },
    beforeCreate:function(){
      if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
        this.$router.push('/Login');
      }else{
          getorganization().then(data =>{
            // console.log(data);
            this.form.livepath = data.livepath;
          });
      }
    },
    methods:{
      openSuccess(text) {
        this.$message({
          message: text,
          type: 'success'
        });
      },
      openError(text) {
        this.$message({
          showClose: true,
          message: text,
          type: 'error'
        });
      },
      verification(){
        if(this.form.rtmp!==''){
          this.tof = false;
        }else{
          this.tof = true;
        }
      },
      OnupdParam(){
        updorganization(this.form).then(data =>{
          if(data.success){
            this.openSuccess(data.resMsg);
          }else{
            this.openError(data.resMsg);
          }
        });
      },
    }
  }
</script>



