
<template>
  <div style="width:100%;height:100%;">
      <div>
        <div class="TopName W95"><i class="el-icon-close" style="margin-right:10px;cursor: pointer;" @click="GoCourseSort"></i>编辑分类</div>
      </div>
      <div class="TopLine"></div>
        <br/>
        <br/>
        <br/>
      <el-form ref="form" :model="form" label-width="150px" >
        <el-form-item label="分类名称">
          <el-input v-model="form.name" style="width: 400px;" @change="verification"></el-input>
        </el-form-item>
        <br/>
        <el-form-item label="分类描述" style="width: 550px;">
          <el-input
            type="textarea"
            :rows="2"
            placeholder="请输入内容" @change="verification"
            v-model="form.desc">
          </el-input>
        </el-form-item>
        <br/>
        <el-form-item label="分类下的课程" style="height:40px;">
          <el-input style="width:295px;height:28px;" :disabled="true" v-model="LnameBlock" @change="verification"></el-input>
          <el-button style="width:100px;height:35px;"  @click="ChoeseL">添加课程</el-button>
        </el-form-item>
        <br/>
        <el-form-item label="" >
          <el-button type="primary"style="width:120px" :disabled="tof" @click="Onaddcategory">提交</el-button>
        </el-form-item>
      </el-form>
      <el-dialog :visible.sync="dialogVisible2" size="small" title="课程选择">
        <div style="width:100%;height:300px;border:1px solid #cdcdcd">
            <div class="TopButton2 W95">
                <div class="TopButtonRight2">
                <!-- <input class="TopButtonRightInput" type="text" placeholder="请输入关键字" /><label class="el-icon-search TopButtonRightSearch"></label>
                <el-button type="primary" size="small" style="margin-left:10px">主要按钮</el-button> -->
                <input class="TopButtonRightInput" type="text" v-model="searchTxt" placeholder="请输入关键字"  @keydown="show($event)" v-on:input ="getcourses" /><label class="el-icon-search TopButtonRightSearch" @click="getsearchTxt"></label>
                <template>
                <label><input name="Fruits" type="radio" value="1" checked @click="getcc(1)"/>已有分类课程</label>
                <label><input name="Fruits" type="radio" value="" @click="getcc(0)"/>未分类课程 </label>
                </template>
                </div>
            </div>
            <div style="width:96%;height:calc(100% - 70px);margin-top:10px;margin-left:2%">

                <div style="width:100%;height:100%;border:1px solid #cdcdcd;overflow-y: auto">
                    <div style="width:70%;height:100%;float:left;">
                        <div style="width:100%;height:40px;line-height:40px;text-align: center;background:#dcdcdc;border-right:1px solid black;">课程名称</div>
                        <div style="width:100%;height:40px;line-height:40px;text-align: center;border-bottom:1px solid #dcdcdc;border-right:1px solid black;" v-for="data in dataL">{{data.name}}</div>
                    </div>
                    <div style="width:calc(30% - 1px);height:100%;float:left;">
                        <div style="width:100%;height:40px;line-height:40px;text-align: center;background:#dcdcdc">选择</div>
                        <div style="width:100%;height:40px;line-height:40px;text-align: center;border-bottom:1px solid #dcdcdc" v-for="data in dataL">
                          <input name="Fruit" type="radio" :value="data.id" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-pagination
            style="margin-right:20px;float:right;float:left"
            :page-size="page.pgLimit"
            layout="prev, pager, next, jumper"
            @current-change="handleCurrentChange"
            :total="page.pgTotalCount">
            </el-pagination>
            <el-button @click="dialogVisible2 = false">取 消</el-button>
            <el-button type="primary" @click="choeseTrueL">提 交</el-button>
        </span>
        </el-dialog>
</div>
</template>

<script>
  import { getcategory,getcourseCategory,addcategory,getOnecategory,getOnecourse,changecategory,updcourse } from '../../../api/api';
export default {
  data () {
    return {
      searchTxt:'',
      page:'',
      form: {
        name: window.localStorage.getItem('saascategoryname'),
        desc: '',
        id:window.localStorage.getItem('saascategoryid')
      },
      radio: '1',
      dialogVisible: false,
      dialogVisible2:false,
      Lid:'',
      Lname:'',
      LidBlock:'',
      LnameBlock:'',
      LidGet:'',
      data: [],
      dataL: [],
      tof:true,
      qwe:0,
    }
  },
  beforeCreate:function(){
    if(window.localStorage.getItem('saasLoginTrueOrFalse')==='false' || window.localStorage.getItem('saasLoginTrueOrFalse')===null){
      this.$router.push('/Login');
    }else{
      getOnecategory(window.localStorage.getItem('saascategoryid')).then(data =>{
        // console.log(data);
        this.form.desc = data.desc;
      })
    }
  },
  methods:{
    openSuccess(text) {
      this.$message({
        message: text,
        type: 'success'
      });
    },
    openError(text) {
      this.$message({
        showClose: true,
        message: text,
        type: 'error'
      });
    },
    GoCourseSort(){
       this.$router.push('/CourseSort');
    },
    ChoeseL(){
      this.dialogVisible2 = true;
      if(this.dataL.length===0){
        this.TofID = '';
        var loginParams = { name: '',pgPageNum:1,category:this.TofID};
        getcourseCategory(loginParams).then(data =>{
          // console.log(data);
          this.page = data;
          this.dataL = data.resList;
        });
      }
    },
    verification(){
      if(this.form.name!==''&&this.form.desc!=''&&this.CidBlock!==''&&this.LidBlock!==''){
        this.tof = false;
      }else{
        this.tof = true;
      }

    },
    getcc(id){
      if(id===0){
        this.TofID = 0;
        var loginParams = { name: this.searchTxt,pgPageNum:1,category:this.TofID};
        getcourseCategory(loginParams).then(data =>{
          // console.log(data);
          this.page = data;
          this.dataL = data.resList;
        });
      }else{
        this.TofID = '';
        var loginParams = { name: this.searchTxt,pgPageNum:1,category:this.TofID};
        getcourseCategory(loginParams).then(data =>{
          // console.log(data);
          this.page = data;
          this.dataL = data.resList;
        });
      }
    },
      handleCurrentChange(currentPage){
        var loginParams = { name: this.searchTxt,pgPageNum:currentPage,category:this.TofID};
        getcourseCategory(loginParams).then(data =>{
          // console.log(data);
          this.page = data;
          this.dataL = data.resList;
        });
      },
    show(ev){
      if(ev.keyCode===13){
        var loginParams = { name: this.searchTxt,pgPageNum:1,category:this.TofID};
        getcourseCategory(loginParams).then(data =>{
          // console.log(data);
          this.page = data;
          this.dataL = data.resList;
        });
      }
    },
    getcourses(){
        if(this.searchTxt===''){
          var loginParams = { name: this.searchTxt,pgPageNum:1,category:this.TofID};
          getcourseCategory(loginParams).then(data =>{
            // console.log(data);
            this.page = data;
            this.dataL = data.resList;
          });
        }
      },
      getsearchTxt(){
        var loginParams = { name: this.searchTxt,pgPageNum:1,category:this.TofID};
        getcourseCategory(loginParams).then(data =>{
          // console.log(data);
          this.page = data;
          this.dataL = data.resList;
        });
      },
    choeseTrueL(){
      var isAutoSend = document.getElementsByName('Fruit');
            for (var i = 0; i < isAutoSend.length; i++) {
                if (isAutoSend[i].checked == true) {
                    this.LidBlock = isAutoSend[i].value;
                    for(var i=0;i<this.dataL.length;i++){
                    if(this.dataL[i].id === parseInt(this.LidBlock)){
                        // console.log(this.dataL[i]);
                        this.LnameBlock = this.dataL[i].name;
                    }
                    }
                    this.dialogVisible2 = false;
                }
            }
    },
    Onaddcategory(){
      var loginParams = {id:this.form.id,name: this.form.name,desc:this.form.desc,type:'',status:'',sort:''};
      changecategory(loginParams).then(data =>{
        // console.log(data);
        if(data.success){
          if(this.LidBlock!==this.LidGet){
            loginParams = {id:this.LidBlock,name:'',desc:'',status:'',sort:'',category:this.form.id,cover:'',lecturer:''};
            updcourse(loginParams).then(data =>{
              // console.log(data);
              if(data.success){
                this.$router.push('/CourseSort');
              }else{
                this.openError(data.resMsg);
              }
            })
          }
        }else{
          this.openError(data.resMsg);
        }
      });
    }
  }
}
</script>
<style>
.asd{
  background: #dcdcdc;
}
</style>



